import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

import {
	DATA_DELETE_SLO_SLI_SUCCESS_ACTION,
	DATA_DELETE_SLO_SLI_ERROR_ACTION
} from '../constants';

const sloSliDeleteQuery = `mutation($sliSysId: String!, $sloSysId: String!) {
  GlideRecord_Mutation {
    delete_sn_srm_service_level_objective(sys_id: $sloSysId) {
      modified_count
    }
    delete_sn_srm_service_level_indicator(sys_id: $sliSysId) {
      modified_count
    }
  }
}`;

export default createGraphQLEffect(sloSliDeleteQuery, {
	variableList: ['sliSysId', 'sloSysId'],
	successActionType: DATA_DELETE_SLO_SLI_SUCCESS_ACTION,
	errorActionType: DATA_DELETE_SLO_SLI_ERROR_ACTION
});
