import {t} from 'sn-translate';

import {ERROR_BUDGET_TYPE} from '../constants';

import getThresholdSymbol from './getThresholdSymbol';

export default (type, threshold) => {
	const thresholdSymbol = getThresholdSymbol(type);
	if (type === ERROR_BUDGET_TYPE)
		return t('{0} {1} {2}%', type, thresholdSymbol, threshold);

	return t('{0} {1} {2}', type, thresholdSymbol, threshold);
};
