import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

import {
	CHART_FOR_PERIOD_FETCH_SUCCESS_ACTION,
	CHART_FOR_PERIOD_FETCH_ERROR_ACTION,
} from '../constants';

const chartForPeriodFetchQuery = `query($query: String!) {
  GlideRecord_Query {
    sn_srm_service_level_objective_history(queryConditions: $query, pagination: {limit: 50}) {
      _results {
        service_level_objective {
          value
        }
        historical_date {
          value
        }
        error_budget_remaining {
          value
        }
        breach_duration_total {
          value
        }
        breach_occurrences {
          value
        }
        breach_occurrences_remaining {
          value
        }
      }
    }
  }
}`;

export default createGraphQLEffect(chartForPeriodFetchQuery, {
	variableList: ['query'],
	successActionType: CHART_FOR_PERIOD_FETCH_SUCCESS_ACTION,
	errorActionType: CHART_FOR_PERIOD_FETCH_ERROR_ACTION
});
