import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

import {
	NEW_INDICATOR_DATA_FETCH_SUCCESS_ACTION,
	NEW_INDICATOR_DATA_FETCH_ERROR_ACTION,
} from '../constants';

const newSliSloFetchQuery = `query {
  tableMeta: GlideRecord_Query {
    sn_srm_service_level_indicator {
      _table_metadata {
        canWrite
        canCreate
        canRead
        canDelete
      }
    }
    sn_srm_service_level_objective {
      _table_metadata {
        canWrite
        canCreate
        canRead
        canDelete
      }
    }
    sn_srm_error_budget_policy_threshold {
      _table_metadata {
        canWrite
        canCreate
        canRead
        canDelete
      }
    }
    sn_srm_error_budget_policy_action {
      _table_metadata {
        canWrite
        canCreate
        canRead
        canDelete
      }
    }
  }
  actionDefinitions: GlideRecord_Query {
    sn_srm_error_budget_policy_action_definition(queryConditions: "active=true") {
      _results {
        sys_id {
          value
        }
        name {
          displayValue
        }
        icon {
          value
        }
        single_use_per_threshold {
          value
        }
      }
    }
  }
}`;

export default createGraphQLEffect(newSliSloFetchQuery, {
	variableList: [],
	successActionType: NEW_INDICATOR_DATA_FETCH_SUCCESS_ACTION,
	errorActionType: NEW_INDICATOR_DATA_FETCH_ERROR_ACTION
});
