import {DATA_DELETE_POLICY_START_ACTION} from '../constants';

export default ({
	action: {
		payload: {
			event: {
				detail: {
					sloSysId,
					policySysId
				}
			}
		}
	},
	properties: {data},
	dispatch,
	updateProperties,
	updateState
}) => {
	const {isNew} = data[sloSysId].policies[policySysId];

	if (isNew) {
		const newData = Object.assign({}, data);
		delete newData[sloSysId].policies[policySysId];

		updateProperties({data: newData});
	} else {
		updateState({deletingPolicy: {sloSysId, policySysId}});
		dispatch(DATA_DELETE_POLICY_START_ACTION, {policySysId});
	}
};
