import moment from 'moment-timezone';
import {createRef} from '@servicenow/ui-renderer-snabbdom';

import {t} from 'sn-translate';

import '@servicenow/now-heading';
import '@servicenow/now-alert';
import '@servicenow/now-modal';
import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import '@servicenow/now-icon';
import '@servicenow/now-card';
import '@servicenow/now-popover';
import '@servicenow/now-highlighted-value';
import '@servicenow/now-loader';
import '@servicenow/now-chart-timeseries';
import '@devsnc/sn-glide-form-controls';
import '@devsnc/sn-declarative-actions';

import sendEventToProvider from '../common/utils/sendEventToProvider';
import {CHANGE_PERIOD_EVENT} from '../constants';

import '../common/components/sn-srm-input-with-label';
import renderCover from '../common/components/submit-cover';
import renderEmptyState from '../common/components/empty-state';
import '../sn-srm-actions';

import {
	SLI_TYPES,
	OPERATOR_TYPES,
	UNITS_TYPES,
	SLO_TYPES,
	SLO_PERIOD_TYPES,
	UPDATE_LOCAL_DATA_ACTION,
	SLI_DESCRIPTION_INPUT_NAME,
	SLI_TYPE_INPUT_NAME,
	SLI_SERVICE_INPUT_NAME,
	SLI_NAME_INPUT_NAME,
	SLI_INTEGRATION_INPUT_NAME,
	SLI_OPERATOR_INPUT_NAME,
	SLI_THRESHOLD_INPUT_NAME,
	SLI_UNITS_INPUT_NAME,
	SLO_TYPE_INPUT_NAME,
	SLO_OBJECTIVE_INPUT_NAME,
	SLO_PERIOD_INPUT_NAME
} from './constants';
import formatDuration from '../common/utils/formatDuration';

const renderChoiceWithLabel = ({name, label, choices, value, required, disabled, isInline}, dispatch) => 
	<div class={{
		'input-with-label': true,
		'input-with-label--inline': isInline
	}}>
		<label htmlFor={name} className="input-with-label__label input-with-label__label--text-input">
			{label}
			{required ? <now-icon icon="asterisk-outline" size="sm" className="input-with-label__label__icon" /> : null}
		</label>
		<div className="input-with-label__input">
			<sn-record-choice
				name={name}
				choices={choices}
				value={value || choices[0].value}
				onValueChange={newValue => dispatch(UPDATE_LOCAL_DATA_ACTION, {name, update: newValue})}
				readonly={disabled}
				required={required}
				ariaLabel={label}
			/>
		</div>
	</div>;

const renderChartWithHeader = ({
	latestTimestamp,
	periods,
	chart: {name, yFormat, yAxisTitle, currentPeriod, loading, periodsData},
	isDuration,
	accessibilityEnabled,
	accessibilityPatternEnabled,
	sloSysId,
	provider,
	index,
	timezone
}) => {
	let period;
	if (currentPeriod)
		period = currentPeriod;
	else if (periods && periods[0])
		period = periods[0].value;

	let data;
	let ticks;
	let extents;
	if (period) {
		const {
			data: chartForPeriodData,
			ticks: chartForPeriodTicks,
			extents: chartsForPeriodExtents
		} = periodsData[period];

		data = chartForPeriodData;
		ticks = chartForPeriodTicks;
		extents = chartsForPeriodExtents;
	}

	return <div className="chart-with-header">
		<div className="chart-with-header__header">
			<h3 className="chart-with-header__header__heading">{name}</h3>
			{
				periods && periods.length > 1 ? <sn-record-choice
					name={name}
					choices={periods}
					value={period}
					onValueChange={newValue => sendEventToProvider({
						provider,
						eventType: CHANGE_PERIOD_EVENT,
						payload: {
							newPeriod: newValue.value,
							sloSysId,
							chartIndex: index
						}
					})}
					readonly={loading}
					required={false}
					ariaLabel={t('Period for chart {0}', name)}
				/> : null
			}
		</div>
		{data && data.length > 0 ?
			<now-chart-timeseries
				accessibilityConfig={{monochrome:true}}
				alignTicks={true}
				width="auto"
				height={250}
				hideRegister={true}
				registerPosition="bottom"
				extents={extents}
				seriesConfig={{
					foo:{
						x: 'timeStamp',
						y: 'y',
						label: name,
						color: '#0B609D'
					}
				}}
				seriesData={data}
				showTooltip={true}
				xAxisConfig={{
					title: t('Date'),
					tickConfig: {
						format: timestamp => timestamp.toString() === latestTimestamp.toString() ?
							t('Current') :
							moment.utc(timestamp).tz(timezone).format('ddd D')
					}
				}}
				xFormat={
					timestamp => moment.utc(timestamp).tz(timezone).format('D MMMM YYYY | HH:mm:ss')
				}
				xAxisType="timeLocal"
				yAxisConfig={{
					title: yAxisTitle,
					tickConfig: {
						tickValues: ticks
					},
				}}
				yFormat={isDuration ? value => formatDuration(value).duration : yFormat}
				yAxisType="linear"
				tooltipConfig={{yFormat: isDuration ? value => formatDuration(value).highPrecisionDuration : yFormat}}
				accessibilityTable={accessibilityEnabled === 'true'}
				accessibilityMode={accessibilityPatternEnabled === 'true'}
			/> :
			<div className="chart-with-header__warning">
				{
					loading ?
						<now-loader label={t('Loading...')} size="lg" /> :
						renderEmptyState({
							message: t('You don\'t have any data yet.'),
							caption: t('Once data starts coming in, insights on performance will display here.')
						})
				}
			</div>
		}
	</div>;
};

const renderMeasurements = (measurements, type) => {
	if (!measurements)
		return '';
	
	const isDuration = type === 'Duration';
	const measurement = isDuration ? measurements.duration : measurements.count;

	return <div className="sn-srm-indicator__props__slo__measurements">
		{
			measurement && Array.isArray(measurement) ? measurement.map(({name, value, message}) => {
				const buttonRef = createRef();

				return <div className="sn-srm-indicator__props__slo__measurements__card">
					<now-card size="lg" interaction="none">
						<now-card-header
							tagline={{
								label: name,
								level: 3
							}}
							actions={message ? [
								{
									id: 'show-info',
									ref: buttonRef,
									name,
									icon: 'circle-info-outline',
									label: message
								}
							] : []}
						/>
						{
							message ? <now-popover interactionType="none" positionTarget={buttonRef} positions={[{target:'top-center',content:'bottom-center'}]}>
								<sn-content content={message}></sn-content>
							</now-popover> : ''
						}
						<p className="sn-srm-indicator__props__slo__measurements__card__value">{value}</p>
					</now-card>
				</div>;
			}) : null
		}
	</div>;
};

const renderCharts = ({
	charts,
	type,
	period,
	isDuration,
	accessibilityEnabled,
	accessibilityPatternEnabled,
	sloSysId,
	timezone,
	provider
}) => {
	const {latestTimestamp} = charts;
	const chartsFromType = type === 'Duration' ? charts.duration : charts.count;

	let chartsFromPeriod;
	if (chartsFromType && chartsFromType.month && chartsFromType.rolling30Days && chartsFromType.rolling7Days) {
		switch (period) {
			case 'Month':
				chartsFromPeriod = chartsFromType.month;
				break;
			case 'Rolling 30 days':
				chartsFromPeriod = chartsFromType.rolling30Days;
				break;
			case 'Rolling 7 days':
				chartsFromPeriod = chartsFromType.rolling7Days;
				break;
		}

		return chartsFromPeriod.data && chartsFromPeriod.data.length > 0 ?
			chartsFromPeriod.data.map((chart, index) => renderChartWithHeader({
				latestTimestamp,
				periods: chartsFromPeriod.periods,
				chart,
				isDuration,
				accessibilityEnabled,
				accessibilityPatternEnabled,
				sloSysId,
				provider,
				index,
				timezone
			})) :
			null;
	}

	return null;
};

const renderSliSection = ({
	sli: {
		description,
		type: {value: typeValue, displayValue: typeDisplayValue},
		name,
		integration: {value: integrationValue, displayValue: integrationDisplayValue},
		operator: {value: operatorValue, displayValue: operatorDisplayValue},
		threshold,
		units: {value: unitsValue, displayValue: unitsDisplayValue},
	},
	serviceName: stateServiceName,
	canWrite,
	propServiceName,
	isSubmitting,
	invalidFieldsNames,
	dispatch
}) => <div className="sn-srm-indicator__props__sli">
	<now-heading
		label={t('Service Level Indicator (SLI)')}
		level="2"
		variant="header-secondary"
	/>
	<sn-srm-input-with-label
		label={t('Service Level Indicator (SLI)')}
		name={SLI_DESCRIPTION_INPUT_NAME}
		type="text"
		inputValue={description}
		placeholder={t('Degraded performance when HTTP Response Time > 1s')}
		required={true}
		readOnly={false}
		invalidFieldsNames={invalidFieldsNames}
		tooltip={t('A carefully defined quantitative measure of some aspect of the level of service')}
		disabled={isSubmitting || !canWrite}
		actionName={UPDATE_LOCAL_DATA_ACTION}
	/>
	{renderChoiceWithLabel({
		label: t('SLI type'),
		name: SLI_TYPE_INPUT_NAME,
		value: typeValue,
		displayValue: typeDisplayValue,
		choices: SLI_TYPES,
		required: true,
		disabled: isSubmitting || !canWrite
	}, dispatch)}
	<sn-srm-input-with-label
		label={t('Service')}
		name={SLI_SERVICE_INPUT_NAME}
		type="text"
		inputValue={stateServiceName || propServiceName}
		placeholder=""
		required={true}
		readOnly={true}
		invalidFieldsNames={invalidFieldsNames}
		disabled={isSubmitting || !canWrite}
		actionName={UPDATE_LOCAL_DATA_ACTION}
	/>

	<now-heading
		label={t('Application Performance Monitoring (APM) properties')}
		level="3"
		variant="title-secondary"
		className="sn-srm-indicator__props__sli__section-header"
	/>
	<p>
		{t('Please ensure the SLI configured in your APM tool matches the values below exactly.')}
	</p>
	<sn-srm-input-with-label
		label={t('Name')}
		name={SLI_NAME_INPUT_NAME}
		type="text"
		inputValue={name}
		placeholder={t('HTTP Response Time > 1s')}
		required={true}
		readOnly={false}
		invalidFieldsNames={invalidFieldsNames}
		message={t('Name must be unique per service')}
		disabled={isSubmitting || !canWrite}
		actionName={UPDATE_LOCAL_DATA_ACTION}
	/>
	<div className="input-with-label">
		<label htmlFor={SLI_INTEGRATION_INPUT_NAME} className="input-with-label__label input-with-label__label--text-input">
			{t('Source')}
			<now-icon icon="asterisk-outline" size="sm" className="input-with-label__label__icon" />
		</label>
		<div className="input-with-label__input">
			<sn-record-reference-connected
				tableName="sn_srm_service_level_indicator"
				fieldName="data_source"
				recordSysId="-1"
				name={SLI_INTEGRATION_INPUT_NAME}
				value={integrationValue}
				displayValue={integrationDisplayValue}
				placeholder={t('Integration Source for SLI')}
				readOnly={false}
				readonly={isSubmitting || !canWrite}
				invalid={invalidFieldsNames && invalidFieldsNames.indexOf(SLI_INTEGRATION_INPUT_NAME) >= 0}
				onValueChange={
					({value, displayValue}) => dispatch(UPDATE_LOCAL_DATA_ACTION, {name: SLI_INTEGRATION_INPUT_NAME, update: {value, displayValue}})
				}
				disableReferenceQualifier
				fieldType="reference"
				aria-label={t('Source {0}', integrationDisplayValue)}
				aria-required={true}
			/>
		</div>
	</div>

	<div className="sn-srm-indicator__props__sli__threshold">
		{renderChoiceWithLabel({
			label: t('Operator'),
			name: SLI_OPERATOR_INPUT_NAME,
			value: operatorValue,
			displayValue: operatorDisplayValue,
			choices: OPERATOR_TYPES,
			required: true,
			disabled: isSubmitting || !canWrite,
			isInline: true
		}, dispatch)}
		<sn-srm-input-with-label
			label={t('Threshold')}
			name={SLI_THRESHOLD_INPUT_NAME}
			type="number"
			inputValue={threshold}
			placeholder={t('Threshold for SLI metric')}
			required={true}
			readOnly={false}
			invalidFieldsNames={invalidFieldsNames}
			disabled={isSubmitting || !canWrite}
			actionName={UPDATE_LOCAL_DATA_ACTION}
			isInline={true}
		/>
		{renderChoiceWithLabel({
			label: t('Units'),
			name: SLI_UNITS_INPUT_NAME,
			value: unitsValue,
			displayValue: unitsDisplayValue,
			choices: UNITS_TYPES,
			required: true,
			invalidFieldsNames,
			disabled: isSubmitting || !canWrite,
			isInline: true
		}, dispatch)}
	</div>

</div>;

const renderSloSection = ({
	slo: {
		sysId: sloSysId,
		type: {value: typeValue, displayValue: typeDisplayValue},
		objective,
		objectiveOccurrences,
		period: {value: periodValue, displayValue: periodDisplayValue},
		timezone
	},
	measurements,
	charts,
	canWrite,
	userProperties,
	isSubmitting,
	provider,
	dispatch
}) => {
	const isDuration = typeValue === 'Duration';
	
	let accessibilityEnabled;
	let accessibilityPatternEnabled;
	if (userProperties && Array.isArray(userProperties)) {
		const accessibilityEnabledObject = userProperties.filter(({name}) => name === 'glide.ui.accessibility')[0];
		accessibilityEnabled = accessibilityEnabledObject.value;

		const accessibilityPatternEnabledObject = userProperties.filter(({name}) => name === 'glide.ui.report.accessibility.patterns')[0];
		accessibilityPatternEnabled = accessibilityPatternEnabledObject.value;
	}

	return <div className="sn-srm-indicator__props__slo">
		<now-heading
			label={t('Service Level Objective (SLO)')}
			level="2"
			variant="header-secondary"
		/>
		<div className="sn-srm-indicator__props__slo__setup">
			{renderChoiceWithLabel({
				label: t('Objective type'),
				name: SLO_TYPE_INPUT_NAME,
				value: typeValue,
				displayValue: typeDisplayValue,
				choices: SLO_TYPES,
				required: false,
				disabled: isSubmitting || !canWrite,
				isInline: true
			}, dispatch)}
			{renderChoiceWithLabel({
				label: isDuration ? t('Objective period') : t('Limit period'),
				name: SLO_PERIOD_INPUT_NAME,
				value: periodValue,
				displayValue: periodDisplayValue,
				choices: SLO_PERIOD_TYPES,
				required: false,
				disabled: isSubmitting || !canWrite,
				isInline: true
			}, dispatch)}
			<sn-srm-input-with-label
				label={isDuration ? t('Objective (percentage)') : t('Limit (occurrences)')}
				name={SLO_OBJECTIVE_INPUT_NAME}
				type="number"
				inputValue={isDuration ? objective : objectiveOccurrences}
				placeholder={isDuration ? t('Target objective') : t('Limit value')}
				required={false}
				readOnly={false}
				disabled={isSubmitting || !canWrite}
				actionName={UPDATE_LOCAL_DATA_ACTION}
				isPercentage={isDuration}
				isInline={true}
			/>
		</div>
		{
			measurements && Object.keys(measurements).length > 0 ?
				renderMeasurements(measurements, typeValue) :
				renderEmptyState({
					message: t('You don\'t have any data yet.'),
					caption: t('Once you\'ve created your reliability indicator and data starts coming in, insights on performance will display here.')
				})
		}
		{renderCharts({
			charts,
			type: typeValue,
			period: periodValue,
			isDuration,
			accessibilityEnabled,
			accessibilityPatternEnabled,
			sloSysId,
			timezone,
			provider
		})}
	</div>;
};

export default ({
	properties: {
		sloSysId,
		serviceName: propServiceName,
		data,
		userProperties
	},
	provider
}, {dispatch}) => {
	if (data && data[sloSysId]) {
		const {
			serviceSysId,
			sli,
			slo,
			measurements,
			charts,
			canWriteSliSlo: canWrite,
			invalidFieldsNames,
			isSubmitting
		} = data[sloSysId];

		return <div className="sn-srm-indicator">
			<sn-srm-actions sloSysId={sloSysId} provider={provider} />
			{renderCover(isSubmitting)}
			<div className="sn-srm-indicator__props">
				{sli ? renderSliSection({
					sli,
					serviceName: propServiceName,
					canWrite,
					isSubmitting,
					invalidFieldsNames,
					propServiceName,
					serviceSysId,
					dispatch
				}) : ''}
				{slo && measurements && charts ? renderSloSection({
					slo,
					measurements,
					charts,
					canWrite,
					userProperties,
					isSubmitting,
					provider,
					dispatch
				}) : ''}
			</div>
		</div>;
	}
};
