export default ({
	action: {payload},
	state: {
		deletingPolicy: {sloSysId, policySysId}
	},
	properties: {data},
	updateProperties
}) => {
	const modCount = payload.data.GlideRecord_Mutation.delete_sn_srm_error_budget_policy_threshold.modified_count;

	if (modCount > 0 && sloSysId && policySysId) {
		const newData = Object.assign({}, data);

		delete newData[sloSysId].policies[policySysId];

		updateProperties({data: newData});
	}
};
