export default payload => {
	if (payload && payload.data) {
		const {
			sn_srm_service_level_indicator: {
				_table_metadata: sliTableMeta
			},
			sn_srm_service_level_objective: {
				_table_metadata: sloTableMeta
			},
			sn_srm_error_budget_policy_threshold: {
				_table_metadata: policiesTableMeta
			},
			sn_srm_error_budget_policy_action: {
				_table_metadata: actionsTableData
			}
		} = payload.data.tableMeta;

		return {
			canWriteSliSlo: sliTableMeta && sloTableMeta ? sliTableMeta.canWrite && sloTableMeta.canWrite : false,
			canDeleteSliSlo: sliTableMeta && sloTableMeta ? sliTableMeta.canDelete && sloTableMeta.canDelete : false,
			canWritePolicies: policiesTableMeta ? policiesTableMeta.canWrite : false,
			canCreatePolicies: policiesTableMeta ? policiesTableMeta.canCreate : false,
			canReadPolicies: policiesTableMeta ? policiesTableMeta.canRead : false,
			canDeletePolicies: policiesTableMeta ? policiesTableMeta.canDelete : false,
			canWriteActions: actionsTableData ? actionsTableData.canWrite : false,
			canCreateActions: actionsTableData ? actionsTableData.canCreate : false,
			canReadActions: actionsTableData ? actionsTableData.canRead : false,
			canDeleteActions: actionsTableData ? actionsTableData.canDelete : false
		};
	}

	return {};
};
