import {CHART_FOR_PERIOD_FETCH_START_ACTION} from '../constants';

import getChartKeys from '../utils/getChartKeys';

export default ({
	action: {
		payload: {
			event: {
				detail: {newPeriod, sloSysId, chartIndex}
			}
		}
	},
	properties: {data},
	updateState,
	updateProperties,
	dispatch
}) => {
	const newData = Object.assign({}, data);
	const indicatorData = newData[sloSysId];
	const indicatorSloData = indicatorData.slo;

	const {typeKey, periodTypeKey} = getChartKeys(indicatorSloData);
	const chart = indicatorData.charts[typeKey][periodTypeKey].data[chartIndex];
	if (!chart.periodsData[newPeriod]) {
		chart.periodsData[newPeriod] = {
			data: []
		};
		chart.loading = true;

		const newPeriodDates = newPeriod.split(',');
		const startDate = newPeriodDates[0].split(' ')[0];
		const endDate = newPeriodDates[1].split(' ')[0];
		const query = `service_level_objective=${sloSysId}^ORDERBYhistorical_date^historical_dateBETWEENjavascript:gs.dateGenerate("${startDate}","00:00:00")@javascript:gs.dateGenerate("${endDate}","23:59:59")`;
		updateState({fetchingChartForSlo: sloSysId});
		dispatch(CHART_FOR_PERIOD_FETCH_START_ACTION, {sloSysId, query});
	}
	chart.currentPeriod = newPeriod;

	updateProperties({data: newData});
};
