import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import view from './view';
import styles from './_styles.scss';

createCustomElement('sn-srm-input-with-label', {
	renderer: {type: snabbdom},
	view,
	styles,
	properties: {
		label: {default: ''},
		name: {default: ''},
		type: {default: ''},
		inputValue: {default: ''},
		placeholder: {default: ''},
		required: {default: false},
		readOnly: {default: false},
		invalidFieldsNames: {default: []},
		message: {default: ''},
		tooltip: {default: ''},
		disabled: {default: false},
		actionName: {default: ''},
		hideLabel: {default: false},
		isPercentage: {default: false},
		noMargin: {default: false},
		isInline: {default: false}
	},
	initialState: {
		previousValue: ''
	}
});
