import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

import {
	DATA_FETCH_SUCCESS_ACTION,
	DATA_FETCH_ERROR_ACTION,
} from '../constants';

const sliSloFetchQuery = `query($sloSysId: String!) {
  GlideDomain_Query {
    user {
      dateFormat
    }
  }
  tableMeta: GlideRecord_Query {
    sn_srm_service_level_indicator {
      _table_metadata {
        canWrite
        canCreate
        canRead
        canDelete
      }
    }
    sn_srm_service_level_objective {
      _table_metadata {
        canWrite
        canCreate
        canRead
        canDelete
      }
    }
    sn_srm_error_budget_policy_threshold {
      _table_metadata {
        canWrite
        canCreate
        canRead
        canDelete
      }
    }
    sn_srm_error_budget_policy_action {
      _table_metadata {
        canWrite
        canCreate
        canRead
        canDelete
      }
    }
  }
  actionDefinitions: GlideRecord_Query {
    sn_srm_error_budget_policy_action_definition(queryConditions: "active=true") {
      _results {
        sys_id {
          value
        }
        name {
          displayValue
        }
        icon {
          value
        }
        single_use_per_threshold {
          value
        }
      }
    }
  }
  GlideRecord_Query {
    indicatorData: sn_srm_service_level_objective(sys_id: $sloSysId) {
      _results {
        sys_id {
          value
        }
        service {
          displayValue
        }
        type {
          value
          displayValue
        }
        goal {
          value
        }
        period {
          value
          displayValue
        }
        timezone {
          value
        }
        achieved {
          value
        }
        error_budget_total {
          value
        }
        error_budget_remaining {
          value
        }
        error_budget_remaining_percent {
          value
        }
        breach_occurrences_allowed {
          value
        }
        breach_occurrences {
          value
        }
        breach_occurrences_remaining {
          value
        }
        service_level_indicator {
          value
        }
        burn_rate {
          value
        }
        _query {
          policies: GlideRecord_Query {
            sn_srm_error_budget_policy_threshold(queryConditions: "service_level_objective=$$parent.sys_id$$") {
              _results {
                sys_id {
                  value
                }
                type {
                  value
                }
                threshold {
                  value
                }
                _query {
                  actions: GlideRecord_Query {
                    sn_srm_error_budget_policy_action(queryConditions: "error_budget_policy_threshold=$$parent.sys_id$$") {
                      _results {
                        sys_id {
                          value
                        }
                        action_definition {
                          value
                        }
                      }
                    }
                  }
                }
              }
            }
          }
          graphsThisMonth: GlideRecord_Query {
            sn_srm_service_level_objective_history(queryConditions: "service_level_objective=$$parent.sys_id$$^ORDERBYhistorical_date^historical_dateONThis month@javascript:gs.beginningOfThisMonth()@javascript:gs.endOfThisMonth()", pagination: {limit: 50}) {
              _results {
                historical_date {
                  value
                }
                error_budget_remaining {
                  value
                }
                breach_duration_total {
                  value
                }
                breach_occurrences {
                  value
                }
                breach_occurrences_remaining {
                  value
                }
              }
            }
          }
          graphsRolling30Days: GlideRecord_Query {
            sn_srm_service_level_objective_history(queryConditions: "service_level_objective=$$parent.sys_id$$^ORDERBYhistorical_date^historical_dateBETWEENjavascript:gs.beginningOfLast30Days()@javascript:gs.endOfToday()", pagination: {limit: 50}) {
              _results {
                historical_date {
                  value
                }
                error_budget_remaining {
                  value
                }
                breach_duration_total {
                  value
                }
                breach_occurrences {
                  value
                }
                breach_occurrences_remaining {
                  value
                }
              }
            }
          }
          graphsRolling7Days: GlideRecord_Query {
            sn_srm_service_level_objective_history(queryConditions: "service_level_objective=$$parent.sys_id$$^ORDERBYhistorical_date^historical_dateBETWEENjavascript:gs.beginningOfLast7Days()@javascript:gs.endOfToday()", pagination: {limit: 50}) {
              _results {
                historical_date {
                  value
                }
                error_budget_remaining {
                  value
                }
                breach_duration_total {
                  value
                }
                breach_occurrences {
                  value
                }
                breach_occurrences_remaining {
                  value
                }
              }
            }
          }
          firstHistoricDate: GlideRecord_Query {
            sn_srm_service_level_objective_history(queryConditions: "service_level_objective=$$parent.sys_id$$^ORDERBYhistorical_date", pagination: {limit: 1}) {
              _results {
                historical_date {
                  value
                }
              }
            }
          }
          sli: GlideRecord_Query {
            sn_srm_service_level_indicator(queryConditions: "sys_id=$$parent.service_level_indicator$$", pagination: {limit: 1}) {
              _results {
                sys_id {
                  value
                }
                description {
                  displayValue
                }
                type {
                  value
                  displayValue
                }
                name {
                  displayValue
                }
                data_source {
                  value
                  displayValue
                }
                operator {
                  value
                  displayValue
                }
                threshold {
                  value
                }
                units {
                  value
                  displayValue
                }
              }
            }
          }
        }
      }
    }
  }
}`;

export default createGraphQLEffect(sliSloFetchQuery, {
	variableList: ['sloSysId'],
	successActionType: DATA_FETCH_SUCCESS_ACTION,
	errorActionType: DATA_FETCH_ERROR_ACTION
});
