import dateToEpoch from '../utils/dateToEpoch';
import getTicks from '../utils/getTicks';
import getChartKeys from '../utils/getChartKeys';

const processDataPoints = (chartRawData, isDuration, isErrorBudget) => {
	let maxValue = 0;

	const data = chartRawData.map(({
		historical_date: {value: date},
		breach_duration_total: {value: totalBreach},
		error_budget_remaining: {value: availableErrorBudget},
		breach_occurrences: {value: totalBreachOccurrences},
		breach_occurrences_remaining: {value: remainingBreachOccurrences}
	}, index) => {
		const totalBreachValue = isDuration ? dateToEpoch(totalBreach) : totalBreachOccurrences;
		const errorBudgetRemainingValue = isDuration ? dateToEpoch(availableErrorBudget) : remainingBreachOccurrences;

		const y = isErrorBudget ? errorBudgetRemainingValue : totalBreachValue;

		maxValue = Math.max(maxValue, y);

		return {
			timeStamp: dateToEpoch(date),
			x: index,
			y
		};
	});

	return {
		ticks: getTicks(maxValue, isDuration),
		extents: {
			y: [0, maxValue * 1.05]
		},
		data
	};
};

const getChartToUpdate = (data, sloSysId) => {
	const indicatorData = data[sloSysId];

	const {typeKey, periodTypeKey} = getChartKeys(indicatorData.slo);

	const currentlyDisplayedCharts = indicatorData.charts[typeKey][periodTypeKey].data;
	const chartIndex = currentlyDisplayedCharts.map(({loading}) => loading).indexOf(true);

	const chartToUpdate = currentlyDisplayedCharts[chartIndex];

	const isDuration = indicatorData.slo.type.value === 'Duration';

	return {
		isDuration,
		chartToUpdate,
		chartIndex
	};
};

export default ({
	action: {payload},
	properties: {data},
	state: {
		fetchingChartForSlo: sloSysId
	},
	updateState,
	updateProperties
}) => {
	const chartRawData = payload.data.GlideRecord_Query.sn_srm_service_level_objective_history._results;
	const newData = Object.assign({}, data);

	const {
		isDuration,
		chartToUpdate,
		chartIndex
	} = getChartToUpdate(newData, sloSysId);

	chartToUpdate.loading = false;

	if (chartRawData && chartRawData.length > 0) {
		const processedData = processDataPoints(chartRawData, isDuration, chartIndex === 1);
		chartToUpdate.periodsData[chartToUpdate.currentPeriod] = {...processedData};
	} else {
		chartToUpdate.periodsData[chartToUpdate.currentPeriod] = {data: []};
	}

	updateState({fetchingChartForSlo: null});
	updateProperties({data: newData});
};
