import {t} from 'sn-translate';

import {DELETE_NAME} from '../../constants';

import {DELETE_ACTION_NAME} from '../constants';

export default ({
	action: {
		payload: {
			event: {
				detail: {
					sloSysId,
					action: name
				}
			}
		}
	},
	updateState
}) => {
	if (name === DELETE_NAME) {
		updateState({
			confirmationModal: {
				isVisible: true,
				header: t('Are you sure you want to delete?'),
				content: t('This will erase both your reliability indicator and error budget policy.'),
				primaryButton: {
					label: t('Delete'),
					variant: 'primary-negative',
					actionName: DELETE_ACTION_NAME
				}
			},
			sloToDelete: sloSysId
		});
	}
};
