import {
	CONFIRMATION_OK_NAME,
	DATA_DELETE_SLO_SLI_START_ACTION,
	SAVE_ACTION_NAME,
	DELETE_ACTION_NAME
} from '../constants';

import saveSlo from '../utils/saveSlo';

const deleteSlo = ({
	data,
	sloSysId,
	updateState,
	dispatch
}) => {
	const {
		sli: {sysId: sliSysId}
	} = data[sloSysId];

	updateState({confirmationModal: null});
	dispatch(DATA_DELETE_SLO_SLI_START_ACTION, {sliSysId, sloSysId});
};

export default ({
	state: {sloToDelete, savingSlo},
	properties: {data},
	action: {
		payload: {action: {name, actionName}}
	},
	updateState,
	updateProperties,
	dispatch
}) => {
	updateState({
		path: 'confirmationModal.isVisible',
		value: false,
		operation: 'set'
	});

	if (name === CONFIRMATION_OK_NAME) {
		updateState({isSubmitting: true});

		switch(actionName) {
			case SAVE_ACTION_NAME:
				saveSlo({
					data,
					sloSysId: savingSlo,
					updateState,
					updateProperties,
					dispatch
				});
				break;
			case DELETE_ACTION_NAME:
				deleteSlo({
					data,
					sloSysId: sloToDelete,
					updateState,
					dispatch
				});
				break;
		}
	}
};
