import {MORE_ACTIONS_EVENT, DELETE_NAME} from '../../constants';
import sendEventToProvider from '../../common/utils/sendEventToProvider';

export default ({
	properties: {sloSysId, provider}
}) => {
	sendEventToProvider({
		provider,
		eventType: MORE_ACTIONS_EVENT,
		payload: {
			sloSysId,
			action: DELETE_NAME
		}
	});
};