import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {PROVIDER_ID} from '../constants';

import view from './view';
import styles from './_styles.scss';

import sendSaveActionToProvider from './effects/sendSaveActionToProvider';
import sendMoreActionsToProvider from './effects/sendMoreActionsToProvider';

createCustomElement('sn-srm-actions', {
	renderer: {type: snabbdom},
	view,
	styles,
	properties: {
		sloSysId: {default: ''},
		provider: {default: null},
		data: {default: `@${PROVIDER_ID}/data`}
	},
	initialState: {
	},
	actionHandlers: {
		'NOW_BUTTON#CLICKED': sendSaveActionToProvider,
		'NOW_DROPDOWN#ITEM_CLICKED': sendMoreActionsToProvider
	}
});
