export default ({
	type: {value: sloType},
	period: {value: sloPeriodType}
}) => {
	const typeKey = sloType === 'Duration' ? 'duration' : 'count'; 
	
	let periodTypeKey;
	switch (sloPeriodType) {
		case 'Month':
			periodTypeKey = 'month';
			break;
		case 'Rolling 30 days':
			periodTypeKey = 'rolling30Days';
			break;
		case 'Rolling 7 days':
			periodTypeKey = 'rolling7Days';
			break;
	}

	return {
		typeKey,
		periodTypeKey
	};
};
