import {t} from 'sn-translate';

const sloTable = 'sn_srm_service_level_objective';

export default ({
	action: {
		payload: {result: sloSysId}
	},
	state: {savingSlo},
	properties: {data}
}) => {
	if (sloSysId) {
		if (data[sloSysId]) {
			data[sloSysId].componentDispatch('GFORM#RELOAD');
		} else {
			setTimeout(() => {
				if (window.g_aw) {
					window.g_aw.closeRecord();
					window.g_aw.openRecord(sloTable, sloSysId);
				}

				// data[savingSlo].componentDispatch('PREVIEW_RECORD', {
				// 	table: sloTable,
				// 	sys_id: sloSysId,
				//	status: 'inserted'
				// });
			}, 100);
		}

		data[savingSlo].componentDispatch('ADD_NOTIFICATIONS', {
			notifications: [{
				type: 'info',
				message: t('Your reliability indicator has been saved!')
			}]
		});
	}
};
