import {Fragment, createRef} from '@servicenow/ui-renderer-snabbdom';

import '@servicenow/now-icon';

import validateKey from '../../utils/validateKey';

export default ({
	properties: {
		label,
		name,
		type,
		inputValue,
		placeholder,
		required,
		readOnly,
		invalidFieldsNames,
		message,
		tooltip,
		disabled,
		actionName,
		hideLabel,
		isPercentage,
		noMargin,
		isInline
	},
	previousValue
}, {dispatch, updateState}) => {
	const ref = createRef();
	const isInvalid = invalidFieldsNames && invalidFieldsNames.indexOf(name) >= 0;
	
	return <div class={{
		'input-with-label': true,
		'input-with-label--inline': isInline,
		'input-with-label--no-margin': noMargin,
		'input-with-label--invalid': isInvalid
	}}>
		{!hideLabel ? <label htmlFor={name} className="input-with-label__label input-with-label__label--text-input">
			{label}
			<Fragment>
				{required ? <now-icon icon="asterisk-outline" size="sm" className="input-with-label__label__icon" /> : ''}
				{tooltip ? <now-button-iconic
					icon="circle-info-outline"
					variant="tertiary"
					bare={true}
					size="sm"
					hidePadding={true}
					configAria={{'aria-label': tooltip}}
					tooltipContent={tooltip}
				/> : ''
				}
			</Fragment>
		</label> : ''}
		<div className="input-with-label__input">
			<div class={{
				'input-with-label__input--percentage': isPercentage
			}}>
				<input
					type={type}
					id={name}
					value={inputValue || inputValue === 0 ? inputValue : ''}
					placeholder={placeholder || ''}
					required={required}
					readOnly={readOnly}
					disabled={disabled ? true : null}
					ref={ref}
					className="input-with-label__input__field"
					aria-label={hideLabel ? label : null}
					aria-invalid={isInvalid}
					aria-describedby={message ? `${name}-message` : null}
					on-input={() => {
						const newValue = ref.current.value;
						if (isPercentage && parseFloat(newValue) > 100) {
							ref.current.value = previousValue;
						} else {
							updateState({previousValue: newValue});
							dispatch(actionName, {name, update: newValue});
						}
					}}
					on-keydown={event => validateKey(event, type)}
					on-mousedown={event => event.stopPropagation()}
					min={0}
					max={isPercentage ? 100 : null}
				/>
			</div>
		</div>
		{message ?
			<now-highlighted-value
				label={message}
				color="info"
				variant="tertiary"
				icon="circle-exclamation-outline"
				id={`${name}-message`}
				className="input-with-label__message"
			/>
			: ''
		}
	</div>;
};
