import {t} from 'sn-translate';

import '@servicenow/now-modal';

import {
	DELETE_CANCEL_MODAL_ACTION_NAME,
	DELETE_OK_MODAL_ACTION_NAME,
	DELETE_POLICY_TYPE_NAME
} from '../../constants';

export default ({
	sysId,
	type
}) => <now-modal
	size="sm"
	headerLabel={t('Are you sure you want to delete?')}
	content={type === DELETE_POLICY_TYPE_NAME ?
		t('This will erase this threshold and all associated action(s).') :
		t('This will erase this action.')
	}
	footerActions={[{
		label: 'Delete',
		variant: 'primary-negative',
		actionName: DELETE_OK_MODAL_ACTION_NAME,
		type,
		sysId
	}, {
		label:'Cancel',
		variant:'secondary',
		actionName: DELETE_CANCEL_MODAL_ACTION_NAME,
		type,
		sysId
	}]}
	opened={true}
/>;
