import {t} from 'sn-translate';

import {ADD_EDIT_ACTION_NAME} from '../../constants';

import renderEmptyState from '../../../common/components/empty-state';

export default ({
	slo: {type: {value: sloType}, objective, objectiveOccurrences},
	policies,
	canReadPolicies,
	isLoading,
	focusedElement
}) => {
	if (!isLoading) {
		if (!canReadPolicies) {
			return renderEmptyState({
				message: t('You need to get permission to see this data.'),
				caption: t('Contact your admin for permission.'),
				isAccessDenied: true
			});
		}

		if (sloType === 'Duration' && !objective && objective !== 0) {
			return renderEmptyState({
				message: t('Welcome! You don\'t have an objective setup.'),
				caption: t('Before you can create an error budget policy, you must define your objective in the Details tab.')
			});
		}

		if (sloType === 'Count' && !objectiveOccurrences && objectiveOccurrences !== 0) {
			return renderEmptyState({
				message: t('Welcome! You don\'t have an occurrences limit setup.'),
				caption: t('Before you can create an error budget policy, you must define your occurrences limit in the Details tab.')
			});
		}

		if (policies && Object.keys(policies).length === 0) {
			return renderEmptyState({
				message: t('Welcome! You don\'t have an error budget policy setup.'),
				caption: t('Your error budget policy allows you to set action(s) for your SLO.'),
				action: {
					label: t('Create error budget policy'),
					name: ADD_EDIT_ACTION_NAME
				},
				focusEmptyMessage: focusedElement === 'emptyState'
			});
		}
	}

	return null;
};
