import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';

import '@servicenow/now-icon';
import '@servicenow/now-tooltip';

import generatePolicyName from '../../utils/generatePolicyName';
import focusElement from '../../utils/focusElement';

import {
	ADD_EDIT_ACTION_NAME,
	DELETE_POLICY_ACTION_NAME,
	EDIT_POLICY_ACTION_NAME,
	POLICY_TYPES,
} from '../../constants';

const renderPolicyCards = ({
	policies,
	selectedPolicy,
	actionDefinitions,
	canWritePolicies,
	canDeletePolicies,
	canReadActions
}) => {
	const sortedPolicies = policies ?
		policies.sort(({threshold: thresholdA}, {threshold: thresholdB}) => thresholdA - thresholdB)
		: [];

	const moreActions = [];
	if (canWritePolicies)
		moreActions.push({id: EDIT_POLICY_ACTION_NAME, label: 'Edit threshold'});

	if (canDeletePolicies)
		moreActions.push({id: DELETE_POLICY_ACTION_NAME, label: 'Delete threshold'});

	return <Fragment>
		{sortedPolicies.map(({sysId, type, threshold, actions}) => {
			const morePolicyActions = moreActions.map(action => ({...action, sysId}));
			const actionDefinitionsArray = actions && canReadActions ? Object.values(actions).map(({definition}) => definition).filter((value, index, self) =>
				self.indexOf(value) === index
			) : [];
			const actionsArray = actionDefinitionsArray && actionDefinitionsArray.length > 0 ?
				actionDefinitionsArray.map(definition => actionDefinitions[definition]) : [];
			const policyName = generatePolicyName(type, threshold);
			const listOfActionNames = actionsArray && actionsArray.length > 0 ?
				actionsArray.map(({name}) => name).join(', ') : t('None');

			return <now-card
				size="lg"
				interaction={canReadActions ? 'click' : 'none'}
				append-to-payload={{sysId}}
				configAria={{
					'aria-label': t(
						'Edit actions for: {0}, actions created: {1}',
						policyName,
						listOfActionNames
					)
				}}
				class={{
					policies__policy: true,
					'policies__policy--selected': sysId === selectedPolicy
				}}
			>
				<dl className="policies__policy__props">
					<div className="policies__policy__props__prop">
						<dt className="policies__policy__props__prop__key">
							{t('Threshold')}
						</dt>
						<dd className="policies__policy__props__prop__value">
							{policyName}
						</dd>
					</div>

					<div className="policies__policy__props__prop">
						<dt className="policies__policy__props__prop__key">
							{t('Action(s)')}
						</dt>
						<dd className="policies__policy__props__prop__value">
							{actionsArray && actionsArray.length > 0 ?
								<ul className="policies__policy__props__prop__value__actions">
									{actionsArray.map(({name, icon}) => <Fragment>
										<li
											aria-label={name}
											className="policies__policy__props__prop__value__actions__action"
										>
											<now-icon icon={icon} size="md" />
										</li>
									</Fragment>
									)}
								</ul>:
								t('None')
							}
						</dd>
					</div>
				</dl>
				{morePolicyActions && morePolicyActions.length > 0 ?
					<now-dropdown
						items={morePolicyActions}
						select="none"
						icon="ellipsis-v-outline"
						variant="secondary"
						size="md"
						bare={true}
						hideCaret={true}
						tooltipContent={t('Policy actions')}
						className="policies__policy__actions"
					/> : null
				}
			</now-card>
			;
		})}
	</Fragment>;
};

const renderPoliciesAccordion = ({
	policies,
	selectedPolicy,
	actionDefinitions,
	canWritePolicies,
	canDeletePolicies,
	canReadActions,
	focusedElement,
	updateState
}) =>
	<div
		tabindex="0"
		hook-insert={({elm}) => focusElement({
			elm,
			focusedElement,
			sectionName: 'policies',
			updateState
		})}
		hook-update={(_, {elm}) => focusElement({
			elm,
			focusedElement,
			sectionName: 'policies',
			updateState
		})}
		on-mousedown={event => event.preventDefault()}
		className="policies__accordion"
	>
		<now-accordion
			headingLevel="3"
			triggerIcon={{type:'chevron', position:'end'}}
			hideDividers={true}
		>
			{POLICY_TYPES.map(({value, displayValue}) => {
				const sectionPolicies = policies
					.filter(({type}) => type === value);

				return sectionPolicies.length > 0 ?
					<now-accordion-item
						header={{label: displayValue, weight: 'normal', size: 'lg', variant: 'primary'}}
						expanded={true}
					>
						<div slot="content">
							{renderPolicyCards({
								policies: sectionPolicies,
								selectedPolicy,
								actionDefinitions,
								canWritePolicies,
								canDeletePolicies,
								canReadActions
							})}
						</div>
					</now-accordion-item> :
					null;	
			})}
		</now-accordion>
	</div>;

export default ({
	isGoalDefined,
	policies,
	selectedPolicy,
	actionDefinitions,
	canReadPolicies,
	canWritePolicies,
	canCreatePolicies,
	canDeletePolicies,
	canReadActions,
	focusedElement,
	updateState
}) => {
	if (canReadPolicies && policies && Object.keys(policies).length > 0 && isGoalDefined) {
		const policiesArray = Object.values(policies);

		return <div className="error-budget-policies__section error-budget-policies__section--policies">
			<div className="policies__header">
				<now-heading
					label={t('Threshold(s)')}
					level="2"
					variant="header-tertiary"
					hasNoMargin
					className="policies__header__heading"
				/>
				{canCreatePolicies ?
					<now-button
						label={t('Add Threshold(s)')}
						variant="secondary"
						size="md"
						className="policies__header__button"
						append-to-payload={{actionName: ADD_EDIT_ACTION_NAME}}
					/> : null
				}
			</div>

			{
				policiesArray.length > 0 ? renderPoliciesAccordion({
					policies: policiesArray,
					selectedPolicy,
					actionDefinitions,
					canWritePolicies,
					canDeletePolicies,
					canReadActions,
					focusedElement,
					updateState
				}) : ''
			}
		</div>;
	}

	return null;
};
