export default ({
	action: {payload},
	state: {
		deletingAction: {sloSysId, policySysId, actionSysId}
	},
	properties: {data},
	updateProperties
}) => {
	const modCount = payload.data.GlideRecord_Mutation.delete_sn_srm_error_budget_policy_action.modified_count;

	if (modCount > 0 && sloSysId && policySysId && actionSysId) {
		const newData = Object.assign({}, data);

		delete newData[sloSysId].policies[policySysId].actions[actionSysId];

		updateProperties({data: newData});
	}
};
