export default payload => {
	const actionDefinitionsData = payload.data.actionDefinitions.sn_srm_error_budget_policy_action_definition._results;

	const actionDefinitions = {};
	for (let i = 0; i < actionDefinitionsData.length; i++) {
		const actionDefinition = actionDefinitionsData[i];
		const {
			sys_id: {value: sysId},
			name: {displayValue: name},
			icon: {value: icon},
			single_use_per_threshold: {value: isSingle}
		} = actionDefinition;

		if (sysId) {
			actionDefinitions[sysId] = {
				sysId,
				name,
				isSingle,
				icon
			};
		}
	}

	return {
		actionDefinitions
	};
};
