import {DATA_UPDATE_START_ACTION} from '../constants';

import isObjectWithKeys from '../utils/isObjectWithKeys';

export default ({
	data,
	sloSysId,
	updateState,
	updateProperties,
	dispatch
}) => {
	const newData = Object.assign({}, data);

	const {sli, slo, serviceSysId, policies: rawPolicies} = newData[sloSysId];
	const policies = isObjectWithKeys(rawPolicies) ? Object.values(rawPolicies).map(policy => ({
		...policy,
		actions: Object.values(policy.actions)
	})) : [];
	
	dispatch(DATA_UPDATE_START_ACTION, {body: {sli, slo, serviceSysId, policies}});
	updateState({savingSlo: sloSysId, confirmationModal: null});

	newData[sloSysId].isSubmitting = true;
	updateProperties({data: newData});
};
