import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

import {
	DATA_DELETE_POLICY_SUCCESS_ACTION,
	DATA_DELETE_POLICY_ERROR_ACTION,
} from '../constants';

const deletePolicyQuery = `mutation($policySysId: String!) {
  GlideRecord_Mutation {
    delete_sn_srm_error_budget_policy_threshold(sys_id: $policySysId) {
      modified_count
    }
  }
}`;

export default createGraphQLEffect(deletePolicyQuery, {
	variableList: ['policySysId'],
	successActionType: DATA_DELETE_POLICY_SUCCESS_ACTION,
	errorActionType: DATA_DELETE_POLICY_ERROR_ACTION
});
