import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {PROVIDER_ID} from '../constants';
import '../sn-srm-provider';

import view from './view';
import styles from './_styles.scss';
import {UPDATE_LOCAL_DATA_ACTION} from './constants';
import {
	fetchData,
	patchData,
	informProviderAboutUnmount
} from './effects';

createCustomElement('sn-srm-indicator', {
	renderer: {type: snabbdom},
	view,
	styles,
	properties: {
		sloSysId: {default: null},
		serviceSysId: {default: null},
		serviceName: {default: null},
		isNewRecord: {default: false},
		userProperties: {default: '@userPrefProvider/userPreferences'},
		data: {default: `@${PROVIDER_ID}/data`}
	},
	initialState: {
		provider: null
	},
	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: fetchData,
		[UPDATE_LOCAL_DATA_ACTION]: patchData,
		[actionTypes.COMPONENT_DISCONNECTED]: informProviderAboutUnmount
	}
});
