import {DATA_DELETE_ACTION_START_ACTION} from '../constants';

export default ({
	action: {
		payload: {
			event: {
				detail: {
					sloSysId,
					policySysId,
					actionSysId
				}
			}
		}
	},
	properties: {data},
	dispatch,
	updateProperties,
	updateState
}) => {
	const {isNew} = data[sloSysId].policies[policySysId].actions[actionSysId];

	if(isNew) {
		const newData = Object.assign({}, data);
		delete newData[sloSysId].policies[policySysId].actions[actionSysId];

		updateProperties({data: newData});
	} else {
		updateState({deletingAction: {sloSysId, policySysId, actionSysId}});
		dispatch(DATA_DELETE_ACTION_START_ACTION, {actionSysId});
	}
};
