import {t} from 'sn-translate';

const MILISECONDS_IN_SECOND = 1000;
const MILISECONDS_IN_MINUTE = 60000;
const MILISECONDS_IN_HOUR = MILISECONDS_IN_MINUTE * 60;
const MILISECONDS_IN_DAY = MILISECONDS_IN_HOUR * 24;

const getFormattedDuration = (formatCode, {days, hours, minutes, seconds}) => {
	const formatMap = {
		d: t('{0}d', days),
		dh: t('{0}d {1}h', days, hours),
		dhs: t('{0}d {1}h', days, hours),
		dhmin: t('{0}d {1}h', days, hours),
		dhmins: t('{0}d {1}h', days, hours),
		dmin: t('{0}d {1}min', days, minutes),
		dmins: t('{0}d {1}min', days, minutes),
		ds: t('{0}d', days),
	
		h: t('{0}h', hours),
		hmin: t('{0}h {1}min', hours, minutes),
		hmins: t('{0}h {1}min', hours, minutes),
		hs: t('{0}h', hours),
	
		min: t('{0}min', minutes),
		mins: t('{0}min {1}s', minutes, seconds),
	
		s: t('{0}s', seconds)
	};

	return formatMap[formatCode];
};

const getHighPrecisionFormattedDuration = (formatCode, {days, hours, minutes, seconds}) => {
	const formatMap = {
		d: t('{0}d', days),
		dh: t('{0}d {1}h', days, hours),
		dhs: t('{0}d {1}h {3}s', days, hours, seconds),
		dhmin: t('{0}d {1}h {2}min', days, hours, minutes),
		dhmins: t('{0}d {1}h {2}min {3}s', days, hours, minutes, seconds),
		dmin: t('{0}d {1}min', days, minutes),
		dmins: t('{0}d {1}min {2}s', days, minutes, seconds),
		ds: t('{0}d {1}s', days, seconds),
	
		h: t('{0}h', hours),
		hmin: t('{0}h {1}min', hours, minutes),
		hmins: t('{0}h {1}min {2}s', hours, minutes, seconds),
		hs: t('{0}h {1}s', hours, seconds),
	
		min: t('{0}min', minutes),
		mins: t('{0}min {1}s', minutes, seconds),
	
		s: t('{0}s', seconds)
	};

	return formatMap[formatCode];
};

export default duration => {
	const days = Math.floor(duration / MILISECONDS_IN_DAY);
	let remainingDuration = duration - MILISECONDS_IN_DAY * days;

	const hours = Math.floor(remainingDuration / MILISECONDS_IN_HOUR);
	remainingDuration = remainingDuration - MILISECONDS_IN_HOUR * hours;

	const minutes = Math.floor(remainingDuration / MILISECONDS_IN_MINUTE);
	remainingDuration = remainingDuration - MILISECONDS_IN_MINUTE * minutes;

	const seconds = Math.ceil(remainingDuration / MILISECONDS_IN_SECOND);

	let formatCode = '';

	if (days > 0)
		formatCode = 'd';

	if (hours > 0)
		formatCode = `${formatCode}h`;

	if (minutes > 0)
		formatCode = `${formatCode}min`;
	
	if (seconds > 0)
		formatCode = `${formatCode}s`;

	return {
		duration: formatCode ? getFormattedDuration(formatCode, {days, hours, minutes, seconds}) : '0',
		highPrecisionDuration: formatCode ? getHighPrecisionFormattedDuration(formatCode, {days, hours, minutes, seconds}) : '0'
	};
};
