export default ({
	action: {
		payload: {
			event: {
				detail: {
					sloSysId,
					component
				}
			}
		}
	},
	properties: {data},
	updateProperties,
}) => {
	const newData = Object.assign({}, data);

	const newComponentsMounted = [...newData[sloSysId].componentsMounted];
	const index = newComponentsMounted.indexOf(component);
	if (index >= 0) 
		newComponentsMounted.splice(index, 1);

	if (newComponentsMounted.length === 0) {
		newData[sloSysId] = {
			...newData[sloSysId],
			isSubmitting: false,
			reloadRecord: false,
			componentsMounted: []
		};
	} else {
		newData[sloSysId].componentsMounted = newComponentsMounted;
	}

	updateProperties({data: newData});
};
