import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {
	SET_SLO_ID_EVENT,
	UPDATE_SLI_SLO_EVENT,
	CHANGE_PERIOD_EVENT,
	UPDATE_POLICIES_EVENT,
	UNMOUNT_COMPONENT_EVENT,
	SAVE_DATA_EVENT,
	MORE_ACTIONS_EVENT,
	DELETE_POLICY_EVENT,
	DELETE_ACTION_EVENT
} from '../constants';

import view from './view';
import styles from './_styles.scss';

import {
	DATA_FETCH_START_ACTION,
	DATA_FETCH_SUCCESS_ACTION,
	NEW_INDICATOR_DATA_FETCH_START_ACTION,
	NEW_INDICATOR_DATA_FETCH_SUCCESS_ACTION,
	CHART_FOR_PERIOD_FETCH_START_ACTION,
	CHART_FOR_PERIOD_FETCH_SUCCESS_ACTION,
	DATA_UPDATE_START_ACTION,
	DATA_UPDATE_SUCCESS_ACTION,
	DATA_DELETE_SLO_SLI_START_ACTION,
	DATA_DELETE_SLO_SLI_SUCCESS_ACTION,
	DATA_DELETE_POLICY_START_ACTION,
	DATA_DELETE_ACTION_START_ACTION,
	DATA_DELETE_POLICY_SUCCESS_ACTION,
	DATA_DELETE_ACTION_SUCCESS_ACTION
} from './constants';

import startDataFetch from './effects/startDataFetch';
import processFetchedData from './effects/processFetchedData';
import fetchNewIndicatorData from './dataSources/fetchNewIndicatorData';
import processFetchedNewIndicatorData from './effects/processFetchedNewIndicatorData';
import fetchChartForPeriod from './dataSources/fetchChartForPeriod';
import processChartForPeriod from './effects/processChartForPeriod';
import updateSliSlo from './effects/updateSliSlo';
import updateCurrentPeriod from './effects/updateCurrentPeriod';
import updatePolicies from './effects/updatePolicies';
import validateAndSaveData from './effects/validateAndSaveData';
import handleActionSelect from './effects/handleActionSelect';
import handleConfirmationFooterActions from './effects/handleConfirmationFooterActions';
import updateConfirmationModalVisibility from './effects/updateConfirmationModalVisibility';
import closeIndicatorTab from './effects/closeIndicatorTab';
import handleSuccsessfulUpdate from './effects/handleSuccessfulUpdate';
import removeUnmountedComponent from './effects/removeUnmountedComponent';
import startPolicyDeletion from './effects/startPolicyDeletion';
import handleSuccessfulPolicyDeletion from './effects/handleSuccessfulPolicyDelition';
import startActionDeletion from './effects/startActionDeletion';
import handleSuccessfulActionDeletion from './effects/handleSuccessfulActionDeletion';

import fetchData from './dataSources/fetchData';
import saveData from './dataSources/saveData';
import deleteSlo from './dataSources/deleteSlo';
import deletePolicy from './dataSources/deletePolicy';
import deleteAction from './dataSources/deleteAction';

createCustomElement('sn-srm-provider', {
	renderer: {type: snabbdom},
	view,
	styles,
	properties: {
		data: {
			default: {},
			selectable: true
		}
	},
	initialState: {
		newSloSysId: '',
		tempSloSysId: '',
		sloToDelete: null,
		savingSlo: null,
		fetchingChartForSlo: null,
		deletingPolicy: {},
		deletingAction: {},
		confirmationModal: null
	},
	eventHandlers: [
		{
			events: [SET_SLO_ID_EVENT],
			effect: startDataFetch
		},
		{
			events: [UPDATE_SLI_SLO_EVENT],
			effect: updateSliSlo
		},
		{
			events: [CHANGE_PERIOD_EVENT],
			effect: updateCurrentPeriod
		},
		{
			events: [UPDATE_POLICIES_EVENT],
			effect: updatePolicies
		},
		{
			events: [UNMOUNT_COMPONENT_EVENT],
			effect: removeUnmountedComponent
		},
		{
			events: [SAVE_DATA_EVENT],
			effect: validateAndSaveData
		},
		{
			events: [MORE_ACTIONS_EVENT],
			effect: handleActionSelect
		},
		{
			events: [DELETE_POLICY_EVENT],
			effect: startPolicyDeletion
		},
		{
			events: [DELETE_ACTION_EVENT],
			effect: startActionDeletion
		}
	],
	actionHandlers: {
		[DATA_FETCH_START_ACTION]: fetchData,
		[DATA_FETCH_SUCCESS_ACTION]: processFetchedData,
		[NEW_INDICATOR_DATA_FETCH_START_ACTION]: fetchNewIndicatorData,
		[NEW_INDICATOR_DATA_FETCH_SUCCESS_ACTION]: processFetchedNewIndicatorData,
		[CHART_FOR_PERIOD_FETCH_START_ACTION]: fetchChartForPeriod,
		[CHART_FOR_PERIOD_FETCH_SUCCESS_ACTION]: processChartForPeriod,
		'NOW_MODAL#FOOTER_ACTION_CLICKED': handleConfirmationFooterActions,
		'NOW_MODAL#OPENED_SET': updateConfirmationModalVisibility,
		[DATA_UPDATE_START_ACTION]: saveData,
		[DATA_UPDATE_SUCCESS_ACTION]: handleSuccsessfulUpdate,
		[DATA_DELETE_SLO_SLI_START_ACTION]: deleteSlo,
		[DATA_DELETE_SLO_SLI_SUCCESS_ACTION]: closeIndicatorTab,
		[DATA_DELETE_POLICY_START_ACTION]: deletePolicy,
		[DATA_DELETE_POLICY_SUCCESS_ACTION]: handleSuccessfulPolicyDeletion,
		[DATA_DELETE_ACTION_START_ACTION]: deleteAction,
		[DATA_DELETE_ACTION_SUCCESS_ACTION]: handleSuccessfulActionDeletion
	}
});
