import {
	PROVIDER_ID,
	SET_SLO_ID_EVENT,
	UPDATE_SLI_SLO_EVENT,
	UNMOUNT_COMPONENT_EVENT
} from '../constants';
import sendEventToProvider from '../common/utils/sendEventToProvider';

import {
	SLI_DESCRIPTION_INPUT_NAME,
	SLI_TYPE_INPUT_NAME,
	SLI_NAME_INPUT_NAME,
	SLI_INTEGRATION_INPUT_NAME,
	SLI_OPERATOR_INPUT_NAME,
	SLI_THRESHOLD_INPUT_NAME,
	SLI_UNITS_INPUT_NAME,
	SLO_TYPE_INPUT_NAME,
	SLO_OBJECTIVE_INPUT_NAME,
	SLO_PERIOD_INPUT_NAME
} from './constants';

export const fetchData = ({
	state: {
		provider,
		properties: {sloSysId, serviceSysId, isNewRecord}
	},
	dispatch,
	updateState
}) => {
	let metricsProvider = provider;
	if(!metricsProvider) {
		metricsProvider = document.getElementById(PROVIDER_ID);

		if (!metricsProvider) {
			metricsProvider = document.createElement('sn-srm-provider');
			metricsProvider.setAttribute('component-id', PROVIDER_ID);
			metricsProvider.setAttribute('now-id', PROVIDER_ID);
			metricsProvider.id = PROVIDER_ID;
			document.body.appendChild(metricsProvider);
		}
    
		updateState({provider: metricsProvider});
	}

	sendEventToProvider({
		provider: metricsProvider,
		eventType: SET_SLO_ID_EVENT,
		payload: {component: 'indicator', serviceSysId, sloSysId, isNewRecord, dispatch}
	});
};

const resetChartsCurrentPeriod = charts => {
	const newCharts = JSON.parse(JSON.stringify(charts));

	Object.keys(newCharts).forEach(sloType => {
		const chartsByType = newCharts[sloType];

		Object.keys(chartsByType).forEach(durationType => {
			const chartsByDurationType = chartsByType[durationType];

			chartsByDurationType.data.forEach(chart => {
				chart.loading = false;
				chart.currentPeriod = null;
			});
		});
	});

	return newCharts;
};

export const patchData = ({
	state: {
		provider
	},
	properties: {
		sloSysId,
		data
	},
	action: {
		payload: {name, update}
	}
}) => {
	const {sli, slo, charts, invalidFieldsNames} = data[sloSysId];

	if (name) {
		const invalidFields = invalidFieldsNames ? [...invalidFieldsNames] : [];
		const newSliSloData = {
			sli: JSON.parse(JSON.stringify(sli)) || {},
			slo: JSON.parse(JSON.stringify(slo)) || {},
		};

		const namePositionInInvalidArray = invalidFields.indexOf(name);
		if (namePositionInInvalidArray >= 0 && (update || (update.value && update.displayValue)))
			invalidFields.splice(namePositionInInvalidArray, 1);

		let key;
		let newCharts = charts;
		switch (name) {
			case SLI_DESCRIPTION_INPUT_NAME:
				newSliSloData.sli.description = update;
				break;
			case SLI_TYPE_INPUT_NAME:
				newSliSloData.sli.type = update;
				break;
			case SLI_NAME_INPUT_NAME:
				newSliSloData.sli.name = update;
				break;
			case SLI_INTEGRATION_INPUT_NAME:
				newSliSloData.sli.integration = update;
				break;
			case SLI_OPERATOR_INPUT_NAME:
				newSliSloData.sli.operator = update;
				break;
			case SLI_THRESHOLD_INPUT_NAME:
				newSliSloData.sli.threshold = update;
				break;
			case SLI_UNITS_INPUT_NAME:
				newSliSloData.sli.units = update;
				break;
			case SLO_TYPE_INPUT_NAME:
				newSliSloData.slo.type = update;
				newCharts = resetChartsCurrentPeriod(charts);
				break;
			case SLO_OBJECTIVE_INPUT_NAME:
				key = newSliSloData.slo.type.value === 'Duration' ? 'objective' : 'objectiveOccurrences';
				newSliSloData.slo[key] = update;
				break;
			case SLO_PERIOD_INPUT_NAME:
				newSliSloData.slo.period = update;
				newCharts = resetChartsCurrentPeriod(charts);
				break;
		}
    
		sendEventToProvider({
			provider,
			eventType: UPDATE_SLI_SLO_EVENT,
			payload: {
				...newSliSloData,
				charts: newCharts,
				invalidFieldsNames
			}
		});
	}
};

export const informProviderAboutUnmount = ({
	state: {provider},
	properties: {sloSysId}
}) => {
	sendEventToProvider({
		provider,
		eventType: UNMOUNT_COMPONENT_EVENT,
		payload: {sloSysId, component: 'indicator'}
	});
};
