import {t} from 'sn-translate';

import '../../../common/components/sn-srm-input-with-label';

import {
	ADD_ACTION_NAME,
	ADD_EDIT_CANCEL_MODAL_ACTION_NAME,
	ADD_EDIT_SAVE_MODAL_ACTION_NAME,
	DELETE_POLICY_ACTION_NAME,
	UPDATE_POLICY_TYPE,
	UPDATE_POLICY_THRESHOLD,
	POLICY_TYPES,
	ERROR_BUDGET_TYPE,
	DEFAULT_POLICY_TYPE
} from '../../constants';

import getThresholdSymbol from '../../utils/getThresholdSymbol';
import focusElement from '../../utils/focusElement';


export default ({
	policies,
	invalidPolicies,
	errorMessage,
	isSingleEdit,
	shouldFocusLastPolicyInEdit,
	focusedElement,
	updateState,
	dispatch
}) => {
	const policiesArray = Object.values(policies);
	const isAlertShown = invalidPolicies && invalidPolicies.length > 0 && errorMessage;

	return <now-modal
		size="lg"
		headerLabel={isSingleEdit ? t('Edit Threshold') : t('Add Threshold(s)')}
		footerActions={[
			{label: isSingleEdit ? t('Update') : t('Add'), variant: 'primary', actionName: ADD_EDIT_SAVE_MODAL_ACTION_NAME},
			{label: t('Cancel'), variant: 'secondary', actionName: ADD_EDIT_CANCEL_MODAL_ACTION_NAME}
		]}
		opened={true}
	>
		<defaultSlot>
			<div className="policy-setup">
				{!isSingleEdit ?
					<p class={{
						'policy-setup__instructions': true,
						'policy-setup__instructions--reduce-bottom-margin': !isSingleEdit && isAlertShown
					}}>
						{t('Add threshold(s) to determine when action(s) against your SLO will take place.')}
					</p> : ''
				}
				{isAlertShown ?
					<now-alert
						status="critical"
						icon="info-circle-outline"
						content={errorMessage}
						className="policy-setup__alert"
					/> : ''
				}
				<div class={{
					'policy-setup__row': true,
					'policy-setup__row--no-margin': true
				}}>
					<div class={{
						'error-budget-policies__label': true,
						'policy-setup__row__cell': true
					}}>
						{t('Threshold Type')}
					</div>
					<p className="policy-setup__row__cell policy-setup__row__cell--small policy-setup__row__cell--small--placeholder" aria-hidden="true">
						{'>='}
					</p>
					<div class={{
						'error-budget-policies__label': true,
						'policy-setup__row__cell': true
					}}>
						{t('Threshold')}
					</div>
					{!isSingleEdit ?
						<div class={{
							'policy-setup__row__cell': true,
							'policy-setup__row__cell--button-placeholder': true
						}}/> : ''
					}
				</div>
				<div
					className="policy-setup__policies-container"
					tabindex="-1"
					hook-update={(_, {elm}) => focusElement({
						elm,
						focusedElement,
						sectionName: 'add-edit-actions',
						updateState
					})}
					on-mousedown={event => event.preventDefault()}
				>
					{policiesArray.map(({sysId, type, threshold}, index) => {
						const policyType = type || DEFAULT_POLICY_TYPE;
						const policyName = POLICY_TYPES.filter(({value}) => value === policyType)[0].displayValue;
						const shouldFocus = policiesArray && shouldFocusLastPolicyInEdit && index === policiesArray.length - 1;

						return <div className="policy-setup__row">
							<div className="policy-setup__row__cell">
								<sn-record-choice
									name={sysId}
									choices={POLICY_TYPES}
									value={policyType}
									ariaLabel={t('Threshold Type')}
									onValueChange={newValue => dispatch(UPDATE_POLICY_TYPE, {name: sysId, update: newValue})}
									autofocus={shouldFocus}
									hook-insert={
										() => setTimeout(updateState({shouldFocusLastPolicyInEdit: false}))
									}
								/>
							</div>
							<p className="policy-setup__row__cell policy-setup__row__cell--small">
								{getThresholdSymbol(policyType)}
							</p>
							<div className="policy-setup__row__cell">
								<sn-srm-input-with-label
									label={t('Threshold')}
									name={sysId}
									type="number"
									inputValue={threshold}
									placeholder={t('Threshold value')}
									required={true}
									readOnly={false}
									actionName={UPDATE_POLICY_THRESHOLD}
									hideLabel={true}
									isPercentage={policyType === ERROR_BUDGET_TYPE}
									invalidFieldsNames={invalidPolicies}
									noMargin={true}
									isInline={true}
								/>
							</div>
							{!isSingleEdit ?
								<div className="policy-setup__row__cell policy-setup__row__cell--button">
									<now-button-iconic
										icon="trash-outline"
										variant="tertiary"
										bare={true}
										size="lg"
										configAria={{'aria-label': t('Delete {0} = {1}', policyName, threshold)}}
										tooltipContent={t('Delete')}
										hidePadding={true}
										disabled={policiesArray && policiesArray.length === 1}
										append-to-payload={{actionName: DELETE_POLICY_ACTION_NAME, sysId}}
									/>
								</div> : ''
							}
						</div>;
					})}
				</div>
				{!isSingleEdit ?
					<now-button-bare
						label={t('Add Threshold')}
						variant="secondary"
						size="md"
						iconStart="plus-outline"
						hidePadding={true}
						append-to-payload={{actionName: ADD_ACTION_NAME}}
					/>: ''
				}
			</div>
		</defaultSlot>
	</now-modal>;
};
