import {t} from 'sn-translate';

import {SAVE_NAME} from '../constants';

export default ({
	properties: {
		sloSysId,
		data
	}
}) => {
	const {
		isSubmitting,
		isNewRecord,
		canWriteSliSlo,
		canDeleteSliSlo
	} = data[sloSysId];

	return <div className="sn-srm-actions">
		<now-button
			label={t('Save')}
			variant="secondary"
			size="md"
			disabled={isSubmitting || !canWriteSliSlo}
			append-to-payload={{name: SAVE_NAME}}
		/>
		{
			!isNewRecord && canDeleteSliSlo ?
				<now-dropdown
					items={[{id: 'delete', label: t('Delete')}]} 
					select="none"
					icon="ellipsis-v-outline"
					variant="secondary"
					size="md"
					hideCaret={true}
					disabled={isSubmitting}
					configAria={{
						'aria-label': t('More actions')
					}}
				/> : null
		}
	</div>;
};
