import processTableMetadata from '../utils/processTableMetadata';
import processActionDefinitions from '../utils/processActionDefinitions';

export default ({
	action: {payload},
	state: {tempSloSysId},
	properties: {data},
	updateProperties
}) => {
	const newData = Object.assign({}, data);

	const tableMeta = processTableMetadata(payload);
	const actionDefinitions = processActionDefinitions(payload);
	newData[tempSloSysId] = {
		...newData[tempSloSysId],
		...tableMeta,
		...actionDefinitions,
		isLoading: false
	};

	updateProperties({data: newData});
};
