import '@servicenow/now-heading';
import '@servicenow/now-button';
import '@servicenow/now-card';
import '@servicenow/now-dropdown';
import '@servicenow/now-accordion';
import '@servicenow/now-modal';
import '@servicenow/now-alert';
import '@devsnc/sn-glide-form-controls';

import '../sn-srm-actions';
import renderCover from '../common/components/submit-cover';

import renderAddEditModal from './components/add-edit-modal';
import renderDeleteModal from './components/delete-modal';
import renderNoPoliciesMessage from './components/no-policies-message';
import renderPolicies from './components/policies';
import renderActions from './components/actions';

export default ({
	properties: {
		sloSysId,
		data
	},
	editedPolicies,
	selectedPolicy,
	editModal: {
		isVisible: isEditModlaVisible,
		isSingleEdit
	},
	invalidPolicies,
	errorMessage,
	deleteModal: {
		isVisible: isDeleteModalVisible,
		type,
		sysId
	},
	shouldFocusLastPolicyInEdit,
	focusedElement,
	provider
}, {updateState, dispatch}) => {
	if(data && data[sloSysId]) {
		const {
			slo,
			policies,
			actionDefinitions,
			isSubmitting,
			canWritePolicies,
			canCreatePolicies,
			canReadPolicies,
			canDeletePolicies,
			canCreateActions,
			canReadActions,
			canDeleteActions,
			isLoading
		} = data[sloSysId];
		if (slo) {
			const {type: {value: sloType}, objective, objectiveOccurrences} = slo;

			let isGoalDefined = true;
			if (sloType === 'Duration' && !objective && objective !== 0) 
				isGoalDefined = false;
			if (sloType === 'Count' && !objectiveOccurrences && objectiveOccurrences !== 0) 
				isGoalDefined = false;

			return <div className="error-budget-policies">
				<sn-srm-actions sloSysId={sloSysId} provider={provider} />
				{renderCover(isSubmitting)}
				{renderPolicies({
					isGoalDefined,
					policies,
					selectedPolicy,
					actionDefinitions,
					canReadPolicies,
					canWritePolicies,
					canCreatePolicies,
					canDeletePolicies,
					canReadActions,
					focusedElement,
					updateState
				})}
				{renderActions({
					isGoalDefined,
					policies,
					actions: selectedPolicy && policies[selectedPolicy] ? Object.values(policies[selectedPolicy].actions) : null,
					actionDefinitions,
					selectedPolicy,
					canReadPolicies,
					canReadActions,
					canCreateActions,
					canDeleteActions,
					focusedElement,
					updateState,
					dispatch
				})}
				{renderNoPoliciesMessage({slo, policies, canReadPolicies, isLoading, focusedElement})}
				{isEditModlaVisible ? renderAddEditModal({
					policies: editedPolicies,
					invalidPolicies,
					errorMessage,
					isSingleEdit,
					shouldFocusLastPolicyInEdit,
					focusedElement,
					updateState,
					dispatch
				}) : ''}
				{isDeleteModalVisible ? renderDeleteModal({
					sysId,
					type
				}) : ''}
			</div>;
		}

		return null;
	}
};
