import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {PROVIDER_ID} from '../constants';
import '../sn-srm-provider';

import view from './view';
import styles from './_styles.scss';
import {
	UPDATE_POLICY_TYPE,
	UPDATE_POLICY_THRESHOLD,
	EDIT_MODAL_HEADER_ADD_EDIT,
	CREATE_POLICY_ACTION,
	CHANGE_POLICY_ACTION_DEFINITION
} from './constants';
import {
	registerProvider,
	openAddEditModal,
	handleBareButtonClick,
	updateType,
	updateThreshold,
	deletePolicyFromModal,
	handleModalFooterActions,
	handleModalClosing,
	handlePolicyActions,
	handleActionActions,
	selectClickedPolicy,
	createOrUpdateAction,
	informProviderAboutUnmount
} from './effects';

createCustomElement('sn-srm-error-budget-policies', {
	renderer: {type: snabbdom},
	view,
	styles,
	properties: {
		sloSysId: {default: null},
		serviceSysId: {default: null},
		isNewRecord: {default: false},
		data: {default: `@${PROVIDER_ID}/data`}
	},
	initialState: {
		provider: null,
		editedPolicies: {},
		deletedPolicies: [],
		selectedPolicy: null,
		editModal: {
			isVisible: false,
			isSingleEdit: false
		},
		invalidPolicies: [],
		errorMessage: null,
		deleteModal: {
			isVisible: false,
			type: null,
			sysId: null
		},
		focusedElement: null,
		shouldFocusLastPolicyInEdit: false
	},
	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: registerProvider,
		'NOW_BUTTON#CLICKED': openAddEditModal,
		'NOW_BUTTON_BARE#CLICKED': handleBareButtonClick,
		[UPDATE_POLICY_TYPE]: updateType,
		[UPDATE_POLICY_THRESHOLD]: updateThreshold,
		'NOW_BUTTON_ICONIC#CLICKED': deletePolicyFromModal,
		'NOW_MODAL#FOOTER_ACTION_CLICKED': handleModalFooterActions,
		'NOW_MODAL#OPENED_SET': handleModalClosing,
		'NOW_DROPDOWN#ITEM_CLICKED': handlePolicyActions,
		'NOW_CARD#CLICKED': selectClickedPolicy,
		[CREATE_POLICY_ACTION]: createOrUpdateAction,
		[CHANGE_POLICY_ACTION_DEFINITION]: createOrUpdateAction,
		'NOW_CARD_HEADER#ACTION_CLICKED': handleActionActions,
		[actionTypes.COMPONENT_DISCONNECTED]: informProviderAboutUnmount
	}
});
