export default (value, isDuration) => {
	let ticksCount = value;
	if (isDuration) {
		const minutes = Math.ceil(value / 60000);
		if (minutes > 0 && value >= 60000) {
			ticksCount = minutes;
		} else {
			const seconds = Math.floor(value / 1000);
			ticksCount = seconds;
		}
	}

	ticksCount = ticksCount >= 10 ? 10 : ticksCount;
	const ticksStep = ticksCount === 0 ? 0 : Math.floor(value / ticksCount);

	return Array.from(Array(ticksCount + 1).keys()).map(multiplier => multiplier * ticksStep);
};
