import {t} from 'sn-translate';

import '@servicenow/now-icon';
import '@servicenow/now-card';
import '@devsnc/sn-glide-form-controls';

import renderEmptyState from '../../../common/components/empty-state';

import {
	DELETE_ACTION_TYPE_NAME,
	CREATE_POLICY_ACTION,
	CHANGE_POLICY_ACTION_DEFINITION
} from '../../constants';
import focusElement from '../../utils/focusElement';

const renderActions = ({
	actions,
	actionDefinitions,
	selectedPolicy,
	canCreateActions,
	canDeleteActions,
	focusedElement,
	updateState,
	dispatch
}) => {
	const usedActionDefinitions = Object.values(actions).map(({definition}) => definition);

	const actionDefinitionsArray = Object.values(actionDefinitions).filter(({sysId, isSingle}) => {
		if (isSingle)
			return usedActionDefinitions.indexOf(sysId) === -1;

		return true;
	});

	// const choices = actionDefinitionsArray.map(({sysId, name, icon}) => ({
	// 	value: sysId,
	// 	displayValue: name,
	// 	icon
	// }));

	return <div
		className="actions"
	>
		<div
			tabindex={actions && actions.length > 0 ? '0' : null}
			hook-update={(_, {elm}) => focusElement({
				elm,
				focusedElement,
				sectionName: 'actions',
				updateState
			})}
			className="actions__container"
		>
			{actions.map(({sysId, definition}) => {
				const {name, icon} = actionDefinitions[definition];

				return <now-card size="lg" interaction="none" className="actions__action-card">
					<now-card-header
						heading={{
							label: t('Action'),
							size: 'sm',
							lines: 1,
							variant: 'tertiary'
						}}
						dropdowns={canDeleteActions ? [{
							id: 'more-actions-action',
							items:[
								{
									id: DELETE_ACTION_TYPE_NAME,
									label: t('Delete action'),
									actionSysId: sysId,
									policySysId: selectedPolicy
								}
							],
							size: 'sm',
							icon: 'ellipsis-v-outline',
							label: t('More actions')
						}] : null}
						className="actions__action-card__header"
					/>
					{/* <sn-record-choice
					name="select"
					label={t('Action definition')}
					choices={choices}
					value={definition}
					onValueChange={({value}) => dispatch(CHANGE_POLICY_ACTION_DEFINITION, {
						policySysId: selectedPolicy,
						actionSysId: sysId,
						actionDefinitionSysId: value
					})}
				/> */}
					<p className="actions__action-card__action">
						<now-icon icon={icon} size="lg" />
						{name}
					</p>
				</now-card>;
			})}
		</div>
		{actionDefinitionsArray && actionDefinitionsArray.length > 0 && canCreateActions ?
			<div
				className="actions__definitions"
				tabindex="0"
				hook-update={(_, {elm}) => focusElement({
					elm,
					focusedElement,
					sectionName: 'definitions',
					updateState
				})}
				on-mousedown={event => event.preventDefault()}
			>
				<h3 className="actions__definitions__header">
					{t('Add an action')}
				</h3>
				<div className="actions__definitions__container">
					{actionDefinitionsArray.map(({sysId, name, icon}) =>
						<button
							className="actions__definitions__button"
							on-click={() => dispatch(CREATE_POLICY_ACTION, {
								policySysId: selectedPolicy,
								actionDefinitionSysId: sysId
							})}
						>
							<now-icon icon={icon} size="lg" className="actions__definitions__button__icon" />
							{name}
						</button>
					)}
				</div>
			</div> : ''
		}
	</div>;
};

const renderEmptyMessage = ({canReadActions, totalActionsCount}) => {
	let message = t('Welcome! Select threshold to add/view action(s).');
	let caption = null;
	if (!canReadActions) {
		message = t('You need to get permission to see this data.');
		caption = t('Contact your admin for permission.');
	} else if (totalActionsCount === 0) {
		message = t('Welcome! You don\'t have any action(s).');
		caption = t('Select a threshold to start adding action(s).');
	}

	return renderEmptyState({
		message,
		caption,
		isAccessDenied: !canReadActions,
		showDivider: true
	});
};

export default ({
	isGoalDefined,
	policies,
	actions,
	actionDefinitions,
	selectedPolicy,
	canReadPolicies,
	canReadActions,
	canCreateActions,
	canDeleteActions,
	focusedElement,
	updateState,
	dispatch
}) => {
	if (canReadPolicies && policies && Object.keys(policies).length > 0 && isGoalDefined) {
		const allActions = Object.values(policies)
			.reduce((total, {actions}) => [...total, ...Object.keys(actions)], []);
		const totalActionsCount = allActions ? allActions.length : 0;

		return <div className="error-budget-policies__section error-budget-policies__section--actions">
			<div className="actions__header">
				<now-heading label={t('Action(s)')} level="2" variant="header-tertiary" hasNoMargin />
				<p className="actions__header__caption">
					{t('If there are multiple actions, they will occur at the same time.')}
				</p>
			</div>
			{canReadActions && selectedPolicy && actions ?
				renderActions({
					actions,
					actionDefinitions,
					selectedPolicy,
					canCreateActions,
					canDeleteActions,
					focusedElement,
					updateState,
					dispatch
				}) :
				renderEmptyMessage({canReadActions, totalActionsCount})
			}
		</div>;
	}

	return null;
};
