import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

import {
	DATA_DELETE_ACTION_SUCCESS_ACTION,
	DATA_DELETE_ACTION_ERROR_ACTION,
} from '../constants';

const deletePolicyQuery = `mutation($actionSysId: String!) {
  GlideRecord_Mutation {
    delete_sn_srm_error_budget_policy_action(sys_id: $actionSysId) {
      modified_count
    }
  }
}`;

export default createGraphQLEffect(deletePolicyQuery, {
	variableList: ['actionSysId'],
	successActionType: DATA_DELETE_ACTION_SUCCESS_ACTION,
	errorActionType: DATA_DELETE_ACTION_ERROR_ACTION
});
