import {t} from 'sn-translate';
import moment from 'moment-timezone';

import formatDuration from '../../common/utils/formatDuration';

import {
	BASIC_DURATION_CHARTS,
	BASIC_COUNT_CHARTS,
	GLIDE_DATE_FORMAT
} from '../constants';
import processTableMetadata from '../utils/processTableMetadata';
import processActionDefinitions from '../utils/processActionDefinitions';
import dateToEpoch from '../utils/dateToEpoch';
import getTicks from '../utils/getTicks';

const processRawChartData = ({
	rawData,
	durationData,
	countData,
	periods
}) => {
	if (periods.length > 0) {
		const firstPeriod = periods[0].value;

		let maxDurationCB = 0;
		let maxDurationEB = 0;
		let maxCountCBO = 0;
		let maxCountRBO = 0;

		[durationData[0], durationData[1], countData[0], countData[1]].forEach(config => {
			config.currentPeriod = firstPeriod,
			config.periodsData = {
				[firstPeriod]: {
					data: []
				}
			};
		});

		const durationCB = durationData[0].periodsData[firstPeriod];
		const durationEB = durationData[1].periodsData[firstPeriod];
		const countCBO = countData[0].periodsData[firstPeriod];
		const countRBO = countData[1].periodsData[firstPeriod];

		rawData.forEach(({
			historical_date: {value: date},
			breach_duration_total: {value: totalBreach},
			error_budget_remaining: {value: availableErrorBudget},
			breach_occurrences: {value: totalBreachOccurrences},
			breach_occurrences_remaining: {value: remainingBreachOccurrences}
		}, index) => {
			const basicPointConfig = {
				timeStamp: dateToEpoch(date),
				x: index
			};

			if (totalBreach || totalBreach === 0) {
				const totalBreachEpoch = dateToEpoch(totalBreach || 0);
				maxDurationCB = Math.max(maxDurationCB, totalBreachEpoch);

				durationCB.data.push({...basicPointConfig, y: totalBreachEpoch});
			}

			if (availableErrorBudget || availableErrorBudget === 0) {
				const availableErrorBudgetEpoch = dateToEpoch(availableErrorBudget);
				maxDurationEB = Math.max(maxDurationEB, availableErrorBudgetEpoch);

				durationEB.data.push({...basicPointConfig, y: availableErrorBudgetEpoch});
			}

			if (totalBreachOccurrences || totalBreachOccurrences === 0) {
				maxCountCBO = Math.max(maxCountCBO, totalBreachOccurrences);

				countCBO.data.push({...basicPointConfig, y: totalBreachOccurrences});
			}
			
			const normalisedRemainingBreachOccurrences = remainingBreachOccurrences || 0;
			if (normalisedRemainingBreachOccurrences || normalisedRemainingBreachOccurrences === 0) {
				maxCountRBO = Math.max(maxCountRBO, normalisedRemainingBreachOccurrences);

				countRBO.data.push({...basicPointConfig, y: normalisedRemainingBreachOccurrences});
			}
		});

		durationCB.ticks = getTicks(maxDurationCB, true);
		durationCB.extents = {
			y: [0, maxDurationCB * 1.05]
		};
		durationEB.ticks = getTicks(maxDurationEB, true);
		durationEB.extents = {
			y: [0, maxDurationEB * 1.05]
		};

		countCBO.ticks = getTicks(maxCountCBO);
		countCBO.extents = {
			y: [0, maxCountCBO * 1.05]
		};
		countRBO.ticks = getTicks(maxCountRBO);
		countRBO.extents = {
			y: [0, maxCountRBO * 1.05]
		};
	}
};

const shouldContinueIterating = ({currentDate, startDate, endDate, isMonthlySlo}) => {
	const compareTo = isMonthlySlo ? endDate : startDate;
	if (currentDate.isSame(compareTo))
		return true;
	
	return isMonthlySlo ? currentDate.isBefore(compareTo) : currentDate.isAfter(compareTo);
};

const generatePeriods = ({firstHistoricDate, sloPeriod, dateFormat, timezone}) => {
	const periods = [];
	if (firstHistoricDate) {
		const startDate = moment.utc(firstHistoricDate.historical_date.value, GLIDE_DATE_FORMAT).tz(timezone);
		startDate.set({
			hour: 0,
			minute: 0,
			second: 0,
			millisecond: 0
		});

		const endDate = moment().tz(timezone);
		endDate.set({
			hour: 23,
			minute: 59,
			second: 59,
			millisecond: 59
		});

		const isMonthlySlo = sloPeriod === 'Month';
		const baseDate = isMonthlySlo ? startDate : endDate;
		let currentDate = baseDate.clone();

		while (shouldContinueIterating({currentDate, startDate, endDate, isMonthlySlo})) {
			let periodEnd;
			let newCurrentDate;

			switch (sloPeriod) {
				case 'Month':
					currentDate.startOf('month');
					periodEnd = currentDate.clone().endOf('month');

					newCurrentDate = currentDate.clone().add(1, 'M').startOf('month');
					break;
				case 'Rolling 7 days':
					periodEnd = currentDate.clone();
					currentDate.subtract(7, 'days');

					newCurrentDate = currentDate.clone().subtract(1, 'days');
					break;
				case 'Rolling 30 days':
					periodEnd = currentDate.clone();
					currentDate.subtract(30, 'days');

					newCurrentDate = currentDate.clone().subtract(1, 'days');
					break;
			}

			const momentFormat = dateFormat.split().map(char => char.toUpperCase()).join();
			const periodOption = {
				value: `${currentDate.format(GLIDE_DATE_FORMAT)},${periodEnd.format(GLIDE_DATE_FORMAT)}`,
				displayValue: t('{0} to {1}', currentDate.format(momentFormat), periodEnd.format(momentFormat))
			};

			if (isMonthlySlo)
				periods.unshift(periodOption);
			else
				periods.push(periodOption);
			currentDate = newCurrentDate;
		}
	}

	return periods;
};

const copyObject = object => JSON.parse(JSON.stringify(object));

const generateChartsData = ({
	rawDataMonth,
	rawData30Days,
	rawData7Days,
	user,
	firstHistoricDate,
	timezone
}) => {
	const {dateFormat} = user;
	const monthPeriods = generatePeriods({
		firstHistoricDate,
		sloPeriod: 'Month',
		dateFormat,
		timezone
	});
	const rolling30DaysPeriods = generatePeriods({
		firstHistoricDate,
		sloPeriod: 'Rolling 30 days',
		dateFormat,
		timezone
	});
	const rolling7DaysPeriods = generatePeriods({
		firstHistoricDate,
		sloPeriod: 'Rolling 7 days',
		dateFormat,
		timezone
	});

	let latestDate;
	if (rawDataMonth.length !== 0) 
		latestDate = rawDataMonth[rawDataMonth.length - 1].historical_date.value;
	
	const charts = {
		latestTimestamp: latestDate ? dateToEpoch(latestDate) : 0,
		duration: {
			month: {
				periods: monthPeriods,
				data: copyObject(BASIC_DURATION_CHARTS)
			},
			rolling30Days: {
				periods: rolling30DaysPeriods,
				data: copyObject(BASIC_DURATION_CHARTS)
			},
			rolling7Days: {
				periods: rolling7DaysPeriods,
				data: copyObject(BASIC_DURATION_CHARTS)
			}
		},
		count: {
			month: {
				periods: monthPeriods,
				data: copyObject(BASIC_COUNT_CHARTS)
			},
			rolling30Days: {
				periods: rolling30DaysPeriods,
				data: copyObject(BASIC_COUNT_CHARTS)
			},
			rolling7Days: {
				periods: rolling7DaysPeriods,
				data: copyObject(BASIC_COUNT_CHARTS)
			}
		}
	};

	processRawChartData({
		rawData: rawDataMonth,
		durationData: charts.duration.month.data,
		countData: charts.count.month.data,
		periods: monthPeriods
	});
	processRawChartData({
		rawData: rawData30Days,
		durationData: charts.duration.rolling30Days.data,
		countData: charts.count.rolling30Days.data,
		periods: rolling30DaysPeriods
	});
	processRawChartData({
		rawData: rawData7Days,
		durationData: charts.duration.rolling7Days.data,
		countData: charts.count.rolling7Days.data,
		periods: rolling7DaysPeriods
	});

	return charts;
};

const translateOldTzToNew = timezone => {
	var OLD_TO_NEW_MAP = {
		ACT: 'Australia/Darwin',
		AET: 'Australia/Sydney',
		AGT: 'America/Argentina/Buenos_Aires',
		ART: 'Africa/Cairo',
		AST: 'America/Anchorage',
		BET: 'America/Sao_Paulo',
		BST: 'Asia/Dhaka',
		CAT: 'Africa/Harare',
		CNT: 'America/St_Johns',
		CST: 'America/Chicago',
		CTT: 'Asia/Shanghai',
		EAT: 'Africa/Addis_Ababa',
		ECT: 'Europe/Paris',
		IET: 'America/Indiana/Indianapolis',
		IST: 'Asia/Kolkata',
		JST: 'Asia/Tokyo',
		MIT: 'Pacific/Apia',
		NET: 'Asia/Yerevan',
		NST: 'Pacific/Auckland',
		PLT: 'Asia/Karachi',
		PNT: 'America/Phoenix',
		PRT: 'America/Puerto_Rico',
		PST: 'America/Los_Angeles',
		SST: 'Pacific/Guadalcanal',
		VST: 'Asia/Ho_Chi_Minh'
	};

	return OLD_TO_NEW_MAP[timezone] || timezone;
};

const processPayloadParts = ({
	sliData,
	sloData,
	graphDataThisMonth,
	graphsRolling30Days,
	graphsRolling7Days,
	user,
	firstHistoricDate
}) => {
	const {
		sys_id: {value: sloSysId},
		goal: {value: sloObjective},
		type: sloType,
		service: {displayValue: serviceName},
		breach_occurrences_allowed: {value: sloObjectiveOccurrences},
		period: sloPeriod,
		timezone: {value: rawTimezone},
		achieved: {value: measuredReliability},
		error_budget_total: {value: errorBudget},
		error_budget_remaining: {value: remainingErrorBudget},
		error_budget_remaining_percent: {value: remainingErrorBudgetPercent},
		breach_occurrences: {value: breachOccurrences},
		breach_occurrences_remaining: {value: remainingBreachOccurrences},
		burn_rate: {value: burnRate}
	} = sloData;
	const {
		sys_id: {value: sliSysId},
		description: {displayValue: sliDescription},
		type: sliType,
		name: {displayValue: sliName},
		data_source: sliIntegration,
		operator: sliOperator,
		threshold: {value: sliThreshold},
		units: sliUnits
	} = sliData;
	const timezone = rawTimezone ? translateOldTzToNew(rawTimezone) : 'UTC';

	const graphData = generateChartsData({
		rawDataMonth: graphDataThisMonth,
		rawData30Days: graphsRolling30Days,
		rawData7Days: graphsRolling7Days,
		user,
		firstHistoricDate,
		timezone
	});

	const burnRateText = burnRate || burnRate === 0 ? burnRate : '-';
	const measurements = {
		duration: [
			{
				name: t('Measured reliability'),
				message: t('Current performance against objective'),
				value: measuredReliability || measuredReliability === 0 ? t('{0}%', measuredReliability) : '-'
			},
			{
				name: t('Error budget'),
				message: t('Calculated based on objective and objective period'),
				value: formatDuration(dateToEpoch(errorBudget)).highPrecisionDuration
			},
			{
				name: t('Remaining error budget'),
				message: t('Amount of error budget left to spend'),
				value: (remainingErrorBudgetPercent || remainingErrorBudgetPercent === 0) && remainingErrorBudget ?
					t(
						'{0}% ({1})',
						remainingErrorBudgetPercent,
						formatDuration(dateToEpoch(remainingErrorBudget)).highPrecisionDuration
					) : '-'
			},
			{
				name: t('Burn rate'),
				message: t('How fast you are consuming error budget (ideal burn rate = 1)'),
				value: burnRateText
			}
		],
		count: [
			{
				name: t('Cumulative breach occurrences'),
				message: t('Number of times SLI has breached in objective period'),
				value: breachOccurrences || breachOccurrences === 0 ? breachOccurrences : '-'
			},
			{
				name: t('Remaining breach occurrences'),
				message: t('Calculated based on limit and cumulative breach occurrences'),
				value: (remainingBreachOccurrences || remainingBreachOccurrences === 0) && remainingErrorBudget ?
					t(
						'{0}% ({1})',
						remainingErrorBudgetPercent,
						remainingBreachOccurrences
					) : '-'
			},
			{
				name: t('Burn rate'),
				message: t('How fast you are consuming error budget (ideal burn rate = 1)'),
				value: burnRateText
			}
		]
	};

	const processedSloData = {
		sysId: sloSysId,
		type: sloType,
		objective: sloObjective,
		objectiveOccurrences: sloObjectiveOccurrences,
		period: sloPeriod,
		timezone
	};

	return {
		slo: processedSloData,
		originalSlo: processedSloData,
		sli: {
			sysId: sliSysId,
			description: sliDescription,
			type: sliType,
			name: sliName,
			integration: sliIntegration,
			operator: sliOperator,
			threshold: sliThreshold,
			units: sliUnits
		},
		serviceName,
		measurements,
		charts: graphData
	};
};

const processPolicies = policiesData => {
	const data = policiesData._results;
	const policies = {};

	for (let i = 0; i < data.length; i++) {
		const {
			sys_id: {value: sysId},
			type: {value: type},
			threshold: {value: threshold}
		} = data[i];

		const actionsData = data[i]._query.actions.sn_srm_error_budget_policy_action._results;
		const actions = {};
		for (let a = 0; a < actionsData.length; a++) {
			const {
				sys_id: {value: actionSysId},
				action_definition: {value: definition}
			} = actionsData[a];

			actions[actionSysId] = {
				sysId: actionSysId,
				definition
			};
		}

		policies[sysId] = {
			sysId,
			type,
			threshold,
			actions
		};
	}

	return {
		policies
	};
};

const processSliSlo = payload => {
	const {user} = payload.data.GlideDomain_Query;
	const sloData = payload.data.GlideRecord_Query.indicatorData._results[0];
	const sliData = sloData._query.sli.sn_srm_service_level_indicator._results[0];
	const graphDataThisMonth = sloData._query.graphsThisMonth.sn_srm_service_level_objective_history._results;
	const graphsRolling30Days = sloData._query.graphsRolling30Days.sn_srm_service_level_objective_history._results;
	const graphsRolling7Days = sloData._query.graphsRolling7Days.sn_srm_service_level_objective_history._results;
	const firstHistoricDate = sloData._query.firstHistoricDate.sn_srm_service_level_objective_history._results[0];
	
	const processedIndicatorData = processPayloadParts({
		sliData,
		sloData,
		graphDataThisMonth,
		graphsRolling30Days,
		graphsRolling7Days,
		user,
		firstHistoricDate
	});

	const policiesData = sloData._query.policies.sn_srm_error_budget_policy_threshold;
	const processedPolicies = processPolicies(policiesData);

	return {
		[sloData.sys_id.value]: {
			...processedIndicatorData,
			...processedPolicies
		}
	};
};

export default ({
	action: {payload},
	properties: {data},
	updateProperties
}) => {
	const newData = Object.assign({}, data);

	const tableMeta = processTableMetadata(payload);
	const sliSlo = processSliSlo(payload);
	const actionDefinitions = processActionDefinitions(payload);

	const sloSysId = Object.keys(sliSlo)[0]; 
	newData[sloSysId] = {
		...tableMeta,
		...newData[sloSysId],
		...sliSlo[sloSysId],
		...actionDefinitions,
		isSubmitting: false
	};

	updateProperties({data: newData});
};
