import {t} from 'sn-translate';

import {
	PROVIDER_ID,
	SET_SLO_ID_EVENT,
	UPDATE_POLICIES_EVENT,
	DELETE_POLICY_EVENT,
	DELETE_ACTION_EVENT,
	UNMOUNT_COMPONENT_EVENT
} from '../constants';
import sendEventToProvider from '../common/utils/sendEventToProvider';

import {
	ADD_EDIT_ACTION_NAME,
	ADD_ACTION_NAME,
	DELETE_POLICY_ACTION_NAME,
	DELETE_POLICY_TYPE_NAME,
	DELETE_ACTION_TYPE_NAME,
	ADD_EDIT_CANCEL_MODAL_ACTION_NAME,
	ADD_EDIT_SAVE_MODAL_ACTION_NAME,
	DELETE_CANCEL_MODAL_ACTION_NAME,
	DELETE_OK_MODAL_ACTION_NAME,
	EDIT_POLICY_ACTION_NAME,
	DEFAULT_POLICY_TYPE
} from './constants';
import generateTempId from './utils/generateTempPolicyId';

export const registerProvider = ({
	state: {provider},
	properties: {
		sloSysId,
		serviceSysId,
		isNewRecord
	},
	updateState,
	dispatch
}) => {
	let metricsProvider = provider;
	if(!metricsProvider) {
		metricsProvider = document.getElementById(PROVIDER_ID);

		if (!metricsProvider) {
			metricsProvider = document.createElement('sn-srm-provider');
			metricsProvider.setAttribute('component-id', PROVIDER_ID);
			metricsProvider.setAttribute('now-id', PROVIDER_ID);
			metricsProvider.id = PROVIDER_ID;
			document.body.appendChild(metricsProvider);
		}
    
		updateState({provider: metricsProvider});

		sendEventToProvider({
			provider: metricsProvider,
			eventType: SET_SLO_ID_EVENT,
			payload: {
				sloSysId,
				serviceSysId,
				isNewRecord,
				component: 'eb-policies',
				dispatch
			}
		});
	}
};

const addNewPolicy = policies => {
	const newPolicies = JSON.parse(JSON.stringify(policies));
	const tempPolicyId = generateTempId();

	newPolicies[tempPolicyId] = {
		sysId: tempPolicyId,
		isNew: true,
		type: DEFAULT_POLICY_TYPE,
		threshold: null,
		actions: {}
	};

	return newPolicies;
};

const openModal = (policies, updateState, isSingleEdit) => {
	let editedPolicies = JSON.parse(JSON.stringify(policies));
	if (Object.keys(editedPolicies).length <= 0)
		editedPolicies = addNewPolicy(editedPolicies);

	updateState({
		editModal: {
			isVisible: true,
			isSingleEdit
		},
		editedPolicies
	});
};

export const openAddEditModal = ({
	action: {
		payload: {actionName}
	},
	updateState
}) => {
	if (actionName === ADD_EDIT_ACTION_NAME) 
		openModal({}, updateState);
};

export const handleBareButtonClick = ({
	action: {
		payload: {actionName}
	},
	state: {editedPolicies},
	updateState
}) => {
	switch (actionName) {
		case ADD_ACTION_NAME:
			updateState({
				editedPolicies: addNewPolicy(editedPolicies),
				shouldFocusLastPolicyInEdit: true
			});
			break;
		case ADD_EDIT_ACTION_NAME:
			openModal({}, updateState);
			updateState({focusedElement: 'policies'});
			break;
	}
};

export const updateType = ({
	action: {
		payload: {name, update}
	},
	state: {editedPolicies},
	updateState
}) => {
	const newPolicies = JSON.parse(JSON.stringify(editedPolicies));
	newPolicies[name].type = update && update.value ? update.value : '';

	updateState({editedPolicies: newPolicies});
};

export const updateThreshold = ({
	action: {
		payload: {name, update}
	},
	state: {editedPolicies},
	updateState
}) => {
	const newPolicies = JSON.parse(JSON.stringify(editedPolicies));
	newPolicies[name].threshold = update || update === 0 ? parseFloat(update) : '';

	updateState({editedPolicies: newPolicies});
};

export const deletePolicyFromModal = ({
	action: {
		payload: {actionName, sysId}
	},
	state: {editedPolicies, deletedPolicies},
	updateState
}) => {
	if (actionName === DELETE_POLICY_ACTION_NAME) {
		const newEditedPolicies = JSON.parse(JSON.stringify(editedPolicies));
		const newDeletedPolicies = [...deletedPolicies, sysId];
		delete newEditedPolicies[sysId];

		updateState({
			editedPolicies: newEditedPolicies,
			deletedPolicies: newDeletedPolicies,
			focusedElement: 'add-edit-actions'
		});
	}
};

const resetModalAndPolicies = updateState => updateState({
	editModal: {
		isVisible: false
	},
	editedPolicies: {},
	deletedPolicies: [],
	invalidPolicies: [],
	errorMessage: null
});

const isDuplicatePolicy = ({
	policyType,
	policyThreshold,
	targetPolicyType,
	targetPolicyThreshold
}) =>
	policyType && policyType === targetPolicyType && policyThreshold && policyThreshold === targetPolicyThreshold;

const getDuplicateThresholds = (editedPolicies, policies, duplicates) => {
	const editedPoliciesArray = Object.values(editedPolicies);
	const policiesArray = Object.values(policies);

	if (editedPoliciesArray.length === 0)
		return duplicates;
	
	const {sysId, type, threshold} = editedPoliciesArray[0];

	const duplicatedExistingPolicies = policiesArray
		.filter(({type: policyType, threshold: policyThreshold}) =>
			isDuplicatePolicy({
				policyType,
				policyThreshold,
				targetPolicyType: type,
				targetPolicyThreshold: threshold
			})
		);

	const duplicatedEditedPolicies = editedPoliciesArray
		.filter(({type: policyType, threshold: policyThreshold}) =>
			isDuplicatePolicy({
				policyType,
				policyThreshold,
				targetPolicyType: type,
				targetPolicyThreshold: threshold
			})
		)
		.map(({sysId}) => sysId);
	
	const newPolicies = Object.assign({}, editedPolicies);
	delete newPolicies[sysId];
	for (let i = 0; i < duplicatedEditedPolicies.length; i++) 
		delete newPolicies[duplicatedEditedPolicies[i]];

	let newDuplicates = duplicates;
	if (duplicatedEditedPolicies.length > 1) 
		newDuplicates = [...newDuplicates, ...duplicatedEditedPolicies];
	
	if (duplicatedExistingPolicies.length > 0)
		newDuplicates = [...newDuplicates, sysId];

	return getDuplicateThresholds(newPolicies, policies, newDuplicates);
};

const checkThresholds = (editedPolicies, policies) => {
	const editedPoliciesArray = Object.values(editedPolicies);

	const policiesWithEmptyThresholds = editedPoliciesArray.filter(({threshold}) => !threshold && threshold !== 0).map(({sysId}) => sysId);
	const duplicatePolicies = getDuplicateThresholds(editedPolicies, policies, []);

	let errorMessage;
	if (policiesWithEmptyThresholds.length > 0) {
		if (duplicatePolicies.length > 0)
			errorMessage = t('All threshold values must be original and complete. Please remove duplicates and make sure all thresholds are filled in.');
		else
			errorMessage = t('All threshold values must be complete. Please make sure all thresholds are filled in.');
	} else if (duplicatePolicies.length > 0) {
		errorMessage = t('There are duplicate threshold values. Please remove duplicates.');
	}

	return {
		canSave: policiesWithEmptyThresholds.length <= 0 && duplicatePolicies.length <= 0,
		invalidPolicies: [...policiesWithEmptyThresholds, ...duplicatePolicies],
		errorMessage
	};
};

const closeDeleteModal = updateState => updateState({
	deleteModal: {
		isVisible: false,
		type: null,
		sysId: null
	}
});

export const handleModalFooterActions = ({
	action: {
		payload: {action: {actionName}}
	},
	properties: {sloSysId, data},
	state: {
		provider,
		editedPolicies,
		selectedPolicy,
		deleteModal: {
			type,
			policySysId,
			actionSysId
		}
	},
	updateState
}) => {
	const {policies} = data[sloSysId];

	switch (actionName) {
		case ADD_EDIT_CANCEL_MODAL_ACTION_NAME:
			resetModalAndPolicies(updateState);
			break;
		case ADD_EDIT_SAVE_MODAL_ACTION_NAME:
			const {canSave, invalidPolicies, errorMessage} = checkThresholds(editedPolicies, policies);

			if (canSave) {
				sendEventToProvider({
					provider,
					eventType: UPDATE_POLICIES_EVENT,
					payload: {
						sloSysId,
						updatedPolicies: {
							...policies,
							...editedPolicies
						}
					}
				});
				resetModalAndPolicies(updateState);
			} else {
				updateState({invalidPolicies, errorMessage});
			}
			break;
		case DELETE_CANCEL_MODAL_ACTION_NAME:
			closeDeleteModal(updateState);
			break;
		case DELETE_OK_MODAL_ACTION_NAME:
			let elementToFocus;
			if (type === DELETE_POLICY_TYPE_NAME) {
				sendEventToProvider({
					provider,
					eventType: DELETE_POLICY_EVENT,
					payload: {
						sloSysId,
						policySysId
					}
				});
				elementToFocus = Object.keys(policies).length > 1 ? 'policies' : 'emptyState';
			} else if (type === DELETE_ACTION_TYPE_NAME) {
				sendEventToProvider({
					provider,
					eventType: DELETE_ACTION_EVENT,
					payload: {
						sloSysId,
						policySysId,
						actionSysId
					}
				});
				const {actions} = policies[selectedPolicy];
				elementToFocus = Object.keys(actions).length > 1 ? 'actions' : 'definitions';
			}

			closeDeleteModal(updateState);
			updateState({
				focusedElement: elementToFocus
			});
			break;
	}
};

export const handleModalClosing = ({updateState}) => {
	resetModalAndPolicies(updateState);
	closeDeleteModal(updateState);
};

export const handlePolicyActions = ({
	action: {
		payload: {
			item: {id, sysId}
		}
	},
	properties: {
		sloSysId,
		data
	},
	updateState
}) => {
	const {policies} = data[sloSysId];

	switch (id) {
		case EDIT_POLICY_ACTION_NAME:
			openModal({[sysId]: policies[sysId]}, updateState, true);
			break;
		case DELETE_POLICY_ACTION_NAME:
			updateState({
				deleteModal: {
					isVisible: true,
					type: DELETE_POLICY_TYPE_NAME,
					sloSysId,
					policySysId: sysId
				}
			});
			break;
	}
};

export const selectClickedPolicy = ({
	action: {
		payload: {sysId}
	},
	updateState
}) => updateState({selectedPolicy: sysId});

export const createOrUpdateAction = ({
	action: {
		payload: {
			policySysId,
			actionSysId,
			actionDefinitionSysId
		}
	},
	properties: {
		sloSysId,
		data
	},
	state: {provider},
	updateState
}) => {
	const {policies, actionDefinitions} = data[sloSysId];

	const updatedPolicies = JSON.parse(JSON.stringify(policies));
	const selectedPolicyActions = updatedPolicies[policySysId].actions;

	const actionName = actionSysId || generateTempId();
	selectedPolicyActions[actionName] = {
		sysId: actionName,
		isNew: true,
		definition: actionDefinitionSysId,
		fields: {}
	};

	const usedActionDefinitions = Object.values(selectedPolicyActions).map(({definition}) => definition);

	const actionDefinitionsArray = Object.values(actionDefinitions).filter(({sysId, isSingle}) => {
		if (isSingle)
			return usedActionDefinitions.indexOf(sysId) === -1;

		return true;
	});
	const nextFocusedElement = actionDefinitionsArray.length > 0 ? 'definitions' : 'actions';
	updateState({focusedElement: nextFocusedElement});

	sendEventToProvider({
		provider,
		eventType: UPDATE_POLICIES_EVENT,
		payload: {
			sloSysId,
			updatedPolicies
		}
	});
};

export const handleActionActions = ({
	action: {
		payload: {
			action: {id, actionSysId, policySysId}
		}
	},
	properties: {sloSysId},
	updateState
}) => {
	if (id === DELETE_ACTION_TYPE_NAME) {
		updateState({
			deleteModal: {
				isVisible: true,
				type: DELETE_ACTION_TYPE_NAME,
				sloSysId,
				policySysId,
				actionSysId
			}
		});
	}
};

export const informProviderAboutUnmount = ({
	properties: {sloSysId},
	state: {provider}
}) => {
	sendEventToProvider({
		provider,
		eventType: UNMOUNT_COMPONENT_EVENT,
		payload: {
			sloSysId,
			component: 'eb-policies'
		}
	});
};
