const addComponentToMounted = (component, components) => {
	const newComponents = [...components];

	if (newComponents.indexOf(component) <= 0) 
		newComponents.push(component);
	

	return newComponents;
};

import {
	SLI_TYPES,
	OPERATOR_TYPES,
	UNITS_TYPES,
	SLO_TYPES,
	SLO_PERIOD_TYPES
} from '../../sn-srm-indicator/constants';

import isFalse from '../utils/isFalse';
import {DATA_FETCH_START_ACTION, NEW_INDICATOR_DATA_FETCH_START_ACTION} from '../constants';

export default ({
	action: {
		payload: {event: {
			detail: {
				sloSysId,
				serviceSysId,
				isNewRecord,
				dispatch: componentDispatch,
				component
			}
		}}
	},
	properties: {data},
	dispatch,
	updateProperties,
	updateState
}) => {
	const newData = Object.assign({}, data);

	if (newData[sloSysId] && newData[sloSysId].componentsMounted.length > 0) {
		newData[sloSysId].componentDispatch = componentDispatch;
		newData[sloSysId].componentsMounted = addComponentToMounted(component, newData[sloSysId].componentsMounted);
	} else {
		newData[sloSysId] = {
			serviceSysId,
			isNewRecord: !isFalse(isNewRecord),
			isSubmitting: false,
			componentDispatch,
			componentsMounted: [component]
		};

		if (isFalse(isNewRecord)) {
			dispatch(DATA_FETCH_START_ACTION, {sloSysId});
		} else {
			newData[sloSysId] = {
				...newData[sloSysId],
				sli: {
					sysId: '',
					isNew: true,
					description: '',
					type: SLI_TYPES[0],
					name: '',
					integration: {value: '', displayValue: ''},
					operator: OPERATOR_TYPES[0],
					threshold: '',
					units: UNITS_TYPES[0]
				},
				slo: {
					sysId: sloSysId,
					isNew: true,
					type: SLO_TYPES[0],
					objective: '',
					objectiveOccurrences: '',
					period: SLO_PERIOD_TYPES[0]
				},
				serviceName: null,
				measurements: {},
				charts: [],
				canWriteSliSlo: true,
				canDeleteSliSlo: true,
				policies: {},
				actionDefinitions: null,
				invalidFieldsNames: [],
				isNewRecord: true,
				isSubmitting: false,
				reloadRecord: false,
				isLoading: true
			};
			updateState({tempSloSysId: sloSysId});
			dispatch(NEW_INDICATOR_DATA_FETCH_START_ACTION);
		}
	}

	updateProperties({data: newData});
};
