export default ({message, caption, action, isAccessDenied, showDivider, focusEmptyMessage}) => 
	<div
		className={`empty-message ${showDivider ? 'empty-message--show-top-divider' : ''}`}
		tabindex={focusEmptyMessage ? '0' : null}
		hook-insert={({elm}) => {
			setTimeout(() => {
				if (focusEmptyMessage)
					elm.focus();
			});
		}}
		hook-update={({elm}) => {
			setTimeout(() => {
				if (focusEmptyMessage)
					setTimeout(() => elm.focus(), 100);
			});
		}}
		on-mousedown={event => event.preventDefault()}
	>
		<div
			className={`empty-message__icon ${isAccessDenied ? 'empty-message__icon--access-denied' : ''}`}
		/>
		<p className="empty-message__message">
			{message}
		</p>
		<p className="empty-message__caption">
			{caption}
		</p>
		{action ? <now-button-bare
			label={action.label}
			variant="secondary"
			size="md"
			hidePadding={true}
			append-to-payload={{actionName: action.name}}
		/> : ''}
	</div>;
