import {SAVE_DATA_EVENT, SAVE_NAME} from '../../constants';
import sendEventToProvider from '../../common/utils/sendEventToProvider';

export default ({
	properties: {sloSysId, provider}
}) => {
	sendEventToProvider({
		provider,
		eventType: SAVE_DATA_EVENT,
		payload: {
			sloSysId,
			action: SAVE_NAME
		}
	});
};
