import {t} from 'sn-translate';

import {
	SAVE_NAME,
	REQUIRED_FIELDS_MAP,
	SAVE_ACTION_NAME
} from '../constants';

import saveSlo from '../utils/saveSlo';
import isObjectWithKeys from '../utils/isObjectWithKeys';

const isValidValue = value => value || value === 0;

const validateEnteredData = ({data, sloSysId, updateProperties}) => {
	const newData = Object.assign({}, data);
	const {sli, slo} = data[sloSysId];

	const updatedData = {sli, slo};
	const invalidFieldsNames = [];
	const invalidFieldsLabels = [];
	let canSave = true;

	REQUIRED_FIELDS_MAP.forEach(({label, object, key, name}) => {
		const fieldData = updatedData[object][key];

		let isValid;
		switch (typeof fieldData) {
			case 'string':
				isValid = isValidValue(fieldData) ? true : false;
				break;
			case 'number':
				isValid = isValidValue(fieldData) ? true : false;
				break;
			case 'object':
				isValid = isValidValue(fieldData.value) && fieldData.displayValue;
				break;
		}
		
		if (!isValid) {
			invalidFieldsNames.push(name);
			invalidFieldsLabels.push(label);
			canSave = false;
		}
	});

	if(!canSave) {
		data[sloSysId].componentDispatch('ADD_NOTIFICATIONS', {
			notifications: [{
				type: 'error',
				message: t('The following mandatory fields are not filled in:<br><br>{0}', invalidFieldsLabels.join(', '))
			}]
		});

		newData[sloSysId] = {
			...newData[sloSysId],
			invalidFieldsNames
		};
		updateProperties({data: newData});
	}

	return canSave;
};

const didSloChange = ({data, sloSysId, canSave, updateState}) => {
	let isSloChanged = false;
	const indicatorData = data[sloSysId];

	if (!indicatorData.isNewRecord && canSave) {
		const {slo, originalSlo} = indicatorData;
		const sloProps = Object.keys(slo);

		for (let i = 0; i < sloProps.length; i++) {
			const sloPropertyName = sloProps[i];
			const sloProperty = slo[sloPropertyName];
			const originalSloProperty = originalSlo[sloPropertyName]; 

			if (sloProperty && typeof sloProperty === 'object') 
				isSloChanged = sloProperty.value !== originalSloProperty.value || isSloChanged;
			else 
				isSloChanged = sloProperty !== originalSloProperty || isSloChanged;
		}

		if (isSloChanged) {
			updateState({
				confirmationModal: {
					isVisible: true,
					header: t('Are you sure?'),
					content: t('Updating the SLO during an active measurement period may result in visualization inconsistencies.'),
					primaryButton: {
						label: t('Save'),
						variant: 'primary',
						actionName: SAVE_ACTION_NAME
					}
				}
			});
		}
	}

	return isSloChanged;
};

export default ({
	action: {
		payload: {
			event: {
				detail: {
					sloSysId,
					action: name
				}
			}
		}
	},
	properties: {data},
	dispatch,
	updateState,
	updateProperties
}) => {
	updateState({savingSlo: sloSysId});

	data[sloSysId].componentDispatch('CLEAR_NOTIFICATIONS', {
		notificationAction: {
			type: 'clear'
		}
	});

	const {sli, slo} = data[sloSysId];
	const newData = Object.assign({}, data);

	if (name === SAVE_NAME) {
		updateState({showAlert: false, invalidFieldsLabels: []});

		if (isObjectWithKeys(sli) && isObjectWithKeys(slo))	{
			const canSave = validateEnteredData({data: newData, sloSysId, updateProperties});
			const isSloChanged = didSloChange({data: newData, sloSysId, canSave, updateState});

			if (canSave && !isSloChanged) {
				saveSlo({
					data,
					sloSysId,
					updateState,
					updateProperties,
					dispatch
				});
			}
		}
	}
};
