import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';

import '@servicenow/now-modal';

import {
	CONFIRMATION_OK_NAME,
	CONFIRMATION_CANCEL_NAME
} from './constants';

export default ({
	confirmationModal
}) => <Fragment>
	{confirmationModal ?
		<now-modal
			size="sm"
			headerLabel={confirmationModal.header}
			content={confirmationModal.content}
			opened={confirmationModal.isVisible}
			footerActions={[
				{
					label: confirmationModal.primaryButton.label,
					variant: confirmationModal.primaryButton.variant,
					name: CONFIRMATION_OK_NAME,
					actionName: confirmationModal.primaryButton.actionName
				},
				{
					label: t('Cancel'),
					variant: 'secondary',
					name: CONFIRMATION_CANCEL_NAME
				}
			]}
		/> : null
	}
</Fragment>;
